import { initializeApp, getApps, cert } from 'firebase-admin/app';
import { getAuth } from 'firebase-admin/auth';
import { getFirestore } from 'firebase-admin/firestore';

export function initializeFirebase() {
  // Check if Firebase is already initialized
  if (getApps().length > 0) {
    return;
  }

  try {
    // Initialize Firebase Admin with service account
    if (process.env.FIREBASE_ADMIN_KEY) {
      // Production: Use service account key from environment
      const serviceAccount = JSON.parse(process.env.FIREBASE_ADMIN_KEY);
      
      initializeApp({
        credential: cert(serviceAccount),
        projectId: process.env.MARIA_PROJECT_ID || 'maria-code',
      });
    } else if (process.env.GOOGLE_APPLICATION_CREDENTIALS) {
      // Development: Use service account file
      initializeApp({
        projectId: process.env.MARIA_PROJECT_ID || 'maria-code',
      });
    } else {
      // Fallback for development
      initializeApp({
        projectId: process.env.MARIA_PROJECT_ID || 'maria-code',
      });
    }

    
    // Test the connection
    getAuth();
    getFirestore();
    
  } catch (error) {
    throw error;
  }
}

// Helper functions for common Firebase operations
export async function verifyAuthToken(token: string) {
  try {
    const auth = getAuth();
    return await auth.verifyIdToken(token);
  } catch (error) {
    throw error;
  }
}

export async function getUserByEmail(email: string) {
  try {
    const auth = getAuth();
    return await auth.getUserByEmail(email);
  } catch (error) {
    if (error instanceof Error && 'code' in error && error.code === 'auth/user-not-found') {
      return null;
    }
    throw error;
  }
}

export async function createUser(userData: {
  email: string;
  password?: string;
  displayName?: string;
  emailVerified?: boolean;
}) {
  try {
    const auth = getAuth();
    return await auth.createUser(userData);
  } catch (error) {
    throw error;
  }
}

export async function updateUserClaims(uid: string, claims: Record<string, unknown>) {
  try {
    const auth = getAuth();
    await auth.setCustomUserClaims(uid, claims);
    return true;
  } catch (error) {
    throw error;
  }
}

export function getFirestoreInstance() {
  return getFirestore();
}

// Database initialization - create default collections and indexes
export async function initializeDatabase() {
  try {
    const firestore = getFirestore();
    
    // Initialize default user roles collection if it doesn't exist
    const rolesRef = firestore.collection('roles');
    const rolesSnapshot = await rolesRef.limit(1).get();
    
    if (rolesSnapshot.empty) {
      
      const defaultRoles = [
        {
          id: 'admin',
          name: 'Administrator',
          permissions: ['read', 'write', 'delete', 'admin'],
          description: 'Full system access',
        },
        {
          id: 'editor',
          name: 'Editor',
          permissions: ['read', 'write'],
          description: 'Can create and edit content',
        },
        {
          id: 'viewer',
          name: 'Viewer',
          permissions: ['read'],
          description: 'Read-only access',
        },
      ];

      const batch = firestore.batch();
      defaultRoles.forEach(role => {
        const docRef = rolesRef.doc(role.id);
        batch.set(docRef, role);
      });
      
      await batch.commit();
    }

    // Create composite indexes (these would typically be created via Firebase console or CLI)
    
  } catch (error) {
    throw error;
  }
}