/**
 * AI Execution API Router
 * Handles AI model execution requests
 */

import { z } from 'zod';
import { router, publicProcedure } from '../trpc';
import { TRPCError } from '@trpc/server';
import { mariaAuth } from '../lib/maria-auth';
import { aiExecutionEngine, AIRequestSchema } from '../lib/ai-execution-engine';

// Session token validation middleware
const withAuth = publicProcedure.use(async ({ next, ctx }) => {
  const token = ctx.req?.headers?.authorization?.replace('Bearer ', '');
  
  if (!token) {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Authentication token required',
    });
  }

  try {
    const session = await mariaAuth.getSession(token);
    return next({
      ctx: {
        ...ctx,
        session,
        userId: session.uid,
      },
    });
  } catch {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Invalid or expired session',
    });
  }
});

export const aiExecutionRouter = router({
  // Execute AI request
  execute: withAuth
    .input(AIRequestSchema)
    .mutation(async ({ input, ctx }) => {
      try {
        // Check user's daily/monthly limits
        const canMakeRequest = await mariaAuth.checkLimits(ctx.session.uid, 'dailyRequests') &&
                              await mariaAuth.checkLimits(ctx.session.uid, 'monthlyRequests');
        
        if (!canMakeRequest) {
          throw new TRPCError({
            code: 'TOO_MANY_REQUESTS',
            message: 'Daily or monthly request limit exceeded',
          });
        }

        // Execute AI request
        const response = await aiExecutionEngine.execute(input, ctx.session);
        
        // Track usage
        await mariaAuth.trackUsage(ctx.session.uid);
        
        return {
          success: true,
          data: response,
        };
      } catch (error) {
        if (error instanceof TRPCError) throw error;
        
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: error instanceof Error ? error.message : 'AI execution failed',
        });
      }
    }),

  // Execute streaming AI request
  executeStream: withAuth
    .input(AIRequestSchema.extend({ stream: z.literal(true) }))
    .subscription(async function* ({ input, ctx }) {
      try {
        // Check user's daily/monthly limits
        const canMakeRequest = await mariaAuth.checkLimits(ctx.session.uid, 'dailyRequests') &&
                              await mariaAuth.checkLimits(ctx.session.uid, 'monthlyRequests');
        
        if (!canMakeRequest) {
          throw new TRPCError({
            code: 'TOO_MANY_REQUESTS',
            message: 'Daily or monthly request limit exceeded',
          });
        }

        // Track usage at start
        await mariaAuth.trackUsage(ctx.session.uid);
        
        // Execute streaming request
        for await (const chunk of aiExecutionEngine.executeStream(input, ctx.session)) {
          yield {
            type: 'chunk',
            data: chunk,
          };
        }
        
        yield {
          type: 'done',
          data: null,
        };
      } catch (error) {
        yield {
          type: 'error',
          data: error instanceof Error ? error.message : 'Stream execution failed',
        };
      }
    }),

  // Get available models for user
  getAvailableModels: withAuth.query(async ({ ctx }) => {
    try {
      const userModels = ctx.session.plan.limits.aiModelAccess;
      
      const modelInfo = {
        'gemini-2.5-pro-preview': {
          name: 'Gemini 2.5 Pro',
          provider: 'Google',
          capabilities: ['text', 'multimodal'],
          contextWindow: 128000,
          description: 'Advanced reasoning and analysis',
        },
        'grok-4-latest': {
          name: 'Grok-4',
          provider: 'xAI',
          capabilities: ['text', 'creative'],
          contextWindow: 32000,
          description: 'Creative and humorous responses',
        },
      };

      const availableModels = userModels
        .filter(model => model in modelInfo)
        .map(model => ({
          id: model,
          ...modelInfo[model as keyof typeof modelInfo],
          available: true,
        }));

      return {
        success: true,
        data: availableModels,
      };
    } catch {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: 'Failed to get available models',
      });
    }
  }),

  // Get usage limits and current usage
  getUsageLimits: withAuth.query(async ({ ctx }) => {
    try {
      const dailyOk = await mariaAuth.checkLimits(ctx.session.uid, 'dailyRequests');
      const monthlyOk = await mariaAuth.checkLimits(ctx.session.uid, 'monthlyRequests');
      
      return {
        success: true,
        data: {
          canMakeRequest: dailyOk && monthlyOk,
          limits: {
            daily: {
              current: ctx.session.usage.dailyRequests,
              limit: ctx.session.plan.limits.dailyRequests,
              available: dailyOk,
            },
            monthly: {
              current: ctx.session.usage.monthlyRequests,
              limit: ctx.session.plan.limits.monthlyRequests,
              available: monthlyOk,
            },
          },
          plan: {
            type: ctx.session.plan.type,
            features: ctx.session.plan.features,
          },
        },
      };
    } catch {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: 'Failed to get usage limits',
      });
    }
  }),

  // Get AI execution statistics (admin/dev)
  getStats: withAuth.query(async ({ ctx }) => {
    // Simple admin check
    if (!ctx.session.email.includes('admin') && !ctx.session.email.includes('bonginkan')) {
      throw new TRPCError({
        code: 'FORBIDDEN',
        message: 'Admin access required',
      });
    }

    try {
      const stats = aiExecutionEngine.getStats();
      return {
        success: true,
        data: stats,
      };
    } catch {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: 'Failed to get stats',
      });
    }
  }),

  // Health check for AI models
  healthCheck: publicProcedure.query(async () => {
    try {
      const health = await aiExecutionEngine.healthCheck();
      return {
        success: true,
        data: {
          ...health,
          overall: health.gemini || health.grok,
          timestamp: new Date().toISOString(),
        },
      };
    } catch {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: 'Health check failed',
      });
    }
  }),

  // Development endpoints
  dev: router({
    // Test AI models
    testModels: publicProcedure.mutation(async () => {
      try {
        const testPrompt = 'Respond with exactly: "Hello from AI model test"';
        const results = [];

        // Test Gemini
        try {
          const geminiResponse = await aiExecutionEngine.execute({
            model: 'gemini-2.5-pro-preview',
            prompt: testPrompt,
            temperature: 0.1,
            maxTokens: 50,
            stream: false,
          });
          results.push({
            model: 'gemini-2.5-pro-preview',
            status: 'success',
            response: geminiResponse.content.substring(0, 100),
            processingTime: geminiResponse.processingTimeMs,
          });
        } catch (error) {
          results.push({
            model: 'gemini-2.5-pro-preview',
            status: 'error',
            error: error instanceof Error ? error.message : 'Unknown error',
          });
        }

        // Test Grok
        try {
          const grokResponse = await aiExecutionEngine.execute({
            model: 'grok-4-latest',
            prompt: testPrompt,
            temperature: 0.1,
            maxTokens: 50,
            stream: false,
          });
          results.push({
            model: 'grok-4-latest',
            status: 'success',
            response: grokResponse.content.substring(0, 100),
            processingTime: grokResponse.processingTimeMs,
          });
        } catch (error) {
          results.push({
            model: 'grok-4-latest',
            status: 'error',
            error: error instanceof Error ? error.message : 'Unknown error',
          });
        }

        return {
          success: true,
          data: results,
        };
      } catch {
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to test models',
        });
      }
    }),

    // Clear cache
    clearCache: publicProcedure.mutation(async () => {
      try {
        aiExecutionEngine.clearCache();
        return {
          success: true,
          message: 'Cache cleared successfully',
        };
      } catch {
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to clear cache',
        });
      }
    }),
  }),
});