import { SecretManagerServiceClient } from '@google-cloud/secret-manager';
import { createHmac } from 'crypto';

export interface BloomJWTPayload {
  sub: string; // user email
  iss: string; // issuer
  aud: string; // audience
  neo4j: {
    dbid: string;
    user: string;
    roles: string[];
  };
  iat: number;
  exp: number;
}

export interface BloomURLOptions {
  perspective?: string;
  search?: string;
  ttl?: number; // seconds, default 900 (15 min)
}

export class Neo4jBloomService {
  private secretClient: SecretManagerServiceClient;
  private jwtSecret?: string;
  
  constructor(
    private config: {
      projectId: string;
      neo4jHost: string;
      neo4jDbId: string;
    }
  ) {
    this.secretClient = new SecretManagerServiceClient();
  }

  private async getJWTSecret(): Promise<string> {
    if (this.jwtSecret) return this.jwtSecret;

    const secretName = `projects/${this.config.projectId}/secrets/NEO4J_BLOOM_JWT_SECRET/versions/latest`;
    const [version] = await this.secretClient.accessSecretVersion({ name: secretName });
    
    if (!version.payload?.data) {
      throw new Error('Failed to retrieve JWT secret');
    }

    this.jwtSecret = version.payload.data.toString();
    return this.jwtSecret;
  }

  private base64urlEncode(str: string): string {
    return Buffer.from(str)
      .toString('base64')
      .replace(/=/g, '')
      .replace(/\+/g, '-')
      .replace(/\//g, '_');
  }

  private async generateJWT(payload: BloomJWTPayload): Promise<string> {
    const header = {
      alg: 'HS256',
      typ: 'JWT'
    };

    const encodedHeader = this.base64urlEncode(JSON.stringify(header));
    const encodedPayload = this.base64urlEncode(JSON.stringify(payload));
    
    const secret = await this.getJWTSecret();
    const signature = createHmac('sha256', secret)
      .update(`${encodedHeader}.${encodedPayload}`)
      .digest('base64')
      .replace(/=/g, '')
      .replace(/\+/g, '-')
      .replace(/\//g, '_');

    return `${encodedHeader}.${encodedPayload}.${signature}`;
  }

  async generateBloomURL(
    userEmail: string,
    userRole: 'reader' | 'editor',
    options: BloomURLOptions = {}
  ): Promise<{ url: string; expiresAt: string }> {
    const now = Math.floor(Date.now() / 1000);
    const ttl = options.ttl || 900; // 15 minutes default
    const exp = now + ttl;

    const payload: BloomJWTPayload = {
      sub: userEmail,
      iss: 'maria-platform',
      aud: 'neo4j-bloom',
      neo4j: {
        dbid: this.config.neo4jDbId,
        user: userEmail.split('@')[0] || userEmail, // username from email or full email if no @
        roles: [userRole]
      },
      iat: now,
      exp
    };

    const jwt = await this.generateJWT(payload);
    
    // Build URL
    const baseUrl = `https://${this.config.neo4jHost}/bloom/embed`;
    const params = new URLSearchParams({
      jwt,
      ...(options.perspective && { perspective: options.perspective }),
      ...(options.search && { search: options.search })
    });

    return {
      url: `${baseUrl}?${params.toString()}`,
      expiresAt: new Date(exp * 1000).toISOString()
    };
  }

  async verifyUserAccess(userEmail: string): Promise<'reader' | 'editor' | null> {
    // TODO: Check user permissions from Firestore
    // For now, return reader access for all authenticated users
    return userEmail ? 'reader' : null;
  }
}