import { z } from 'zod';
import { router, protectedProcedure, createErrorHandler, rateLimit } from '../trpc';
import { TRPCError } from '@trpc/server';

// Input schemas
const createTaskPlanSchema = z.object({
  input: z.string().min(1).max(8000),
  context: z.object({
    type: z.enum(['paper', 'presentation', 'project', 'general']).optional(),
    sessionId: z.string().optional(),
    stakeholders: z.array(z.string()).optional(),
    budget: z.number().positive().optional(),
    timeline: z.string().optional(),
  }).optional(),
});

const refineTaskPlanSchema = z.object({
  planId: z.string(),
  feedback: z.string().min(1).max(4000),
  sessionId: z.string().optional(),
});

const executeTaskPlanSchema = z.object({
  planId: z.string(),
  options: z.object({
    autoExecute: z.boolean().default(false),
    sessionId: z.string().optional(),
  }).optional(),
});

const parseRTFSchema = z.object({
  input: z.string().min(1).max(8000),
  context: z.object({
    type: z.string().optional(),
    previousMessages: z.array(z.string()).optional(),
    userProfile: z.record(z.any()).optional(),
  }).optional(),
});

const generateSOWSchema = z.object({
  planId: z.string(),
  options: z.object({
    projectName: z.string().optional(),
    stakeholders: z.array(z.string()).optional(),
    budget: z.number().positive().optional(),
    timeline: z.string().optional(),
    template: z.string().optional(),
  }).optional(),
});

// Output schemas
const taskPlanSchema = z.object({
  id: z.string(),
  title: z.string(),
  description: z.string(),
  rtfStructure: z.record(z.any()),
  sowDocument: z.record(z.any()).optional(),
  executionPlan: z.array(z.object({
    id: z.string(),
    name: z.string(),
    description: z.string(),
    type: z.enum(['research', 'analysis', 'creation', 'review', 'communication']),
    estimatedTime: z.number(),
    prerequisites: z.array(z.string()),
    deliverables: z.array(z.string()),
    status: z.enum(['pending', 'in-progress', 'completed', 'blocked']),
  })),
  status: z.enum(['draft', 'approved', 'in-progress', 'completed', 'cancelled']),
  createdAt: z.date(),
  updatedAt: z.date(),
});

const rtfStructureSchema = z.object({
  role: z.string(),
  task: z.object({
    type: z.enum(['paper', 'presentation', 'project', 'code', 'analysis', 'general']),
    intent: z.string(),
    description: z.string(),
    scope: z.enum(['single-action', 'multi-step', 'iterative', 'collaborative']),
    priority: z.enum(['low', 'medium', 'high', 'urgent']),
    requirements: z.array(z.string()),
    constraints: z.array(z.string()),
    dependencies: z.array(z.string()),
    expectedOutcome: z.string(),
  }),
  format: z.record(z.any()),
  confidence: z.number().min(0).max(1),
  metadata: z.record(z.any()),
});

const executionResultSchema = z.object({
  success: z.boolean(),
  completedSteps: z.array(z.object({
    id: z.string(),
    name: z.string(),
    status: z.string(),
  })),
  errors: z.array(z.string()),
});

export const conversationRouter = router({
  // Parse natural language input into RTF structure
  parseRTF: protectedProcedure
    .use(rateLimit(50, 60 * 60 * 1000)) // 50 requests per hour
    .input(parseRTFSchema)
    .output(rtfStructureSchema)
    .mutation(async ({ input }) => {
      try {
        const { RTFParser, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const rtfParser = new RTFParser(vertexAI);

        const rtfStructure = await rtfParser.parseRTF(input.input, input.context);

        return {
          role: rtfStructure.role,
          task: {
            type: rtfStructure.task.type,
            intent: rtfStructure.task.intent,
            description: rtfStructure.task.description,
            scope: rtfStructure.task.scope,
            priority: rtfStructure.task.priority,
            requirements: rtfStructure.task.requirements,
            constraints: rtfStructure.task.constraints,
            dependencies: rtfStructure.task.dependencies,
            expectedOutcome: rtfStructure.task.expectedOutcome,
          },
          format: rtfStructure.format,
          confidence: rtfStructure.confidence,
          metadata: rtfStructure.metadata,
        };
      } catch (error) {
        throw createErrorHandler('Parse RTF')(error);
      }
    }),

  // Create a task plan from natural language input
  createTaskPlan: protectedProcedure
    .use(rateLimit(20, 60 * 60 * 1000)) // 20 plans per hour
    .input(createTaskPlanSchema)
    .output(taskPlanSchema)
    .mutation(async ({ input }) => {
      try {
        const { PlannerAgent, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const planner = new PlannerAgent(vertexAI);

        const taskPlan = await planner.createTaskPlan(input.input, input.context);

        return {
          id: taskPlan.id,
          title: taskPlan.title,
          description: String(taskPlan.description),
          rtfStructure: taskPlan.rtfStructure || {},
          sowDocument: (taskPlan as any).sowDocument,
          executionPlan: taskPlan.executionPlan || [],
          status: (taskPlan.status as 'draft' | 'approved' | 'in-progress' | 'completed' | 'cancelled') || 'draft',
          createdAt: taskPlan.createdAt,
          updatedAt: taskPlan.updatedAt,
        };
      } catch (error) {
        throw createErrorHandler('Create task plan')(error);
      }
    }),

  // Refine an existing task plan
  refineTaskPlan: protectedProcedure
    .use(rateLimit(50, 60 * 60 * 1000)) // 50 refinements per hour
    .input(refineTaskPlanSchema)
    .output(taskPlanSchema)
    .mutation(async ({ input }) => {
      try {
        const { PlannerAgent, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const planner = new PlannerAgent(vertexAI);

        const refinedPlan = await planner.refinePlan(
          input.planId,
          input.feedback,
          input.sessionId
        );

        return {
          id: refinedPlan.id,
          title: refinedPlan.title,
          description: refinedPlan.description,
          rtfStructure: refinedPlan.rtfStructure,
          sowDocument: refinedPlan.sowDocument,
          executionPlan: refinedPlan.executionPlan,
          status: refinedPlan.status,
          createdAt: refinedPlan.createdAt,
          updatedAt: refinedPlan.updatedAt,
        };
      } catch (error) {
        throw createErrorHandler('Refine task plan')(error);
      }
    }),

  // Execute a task plan
  executeTaskPlan: protectedProcedure
    .use(rateLimit(10, 60 * 60 * 1000)) // 10 executions per hour
    .input(executeTaskPlanSchema)
    .output(executionResultSchema)
    .mutation(async ({ input }) => {
      try {
        const { PlannerAgent, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const planner = new PlannerAgent(vertexAI);

        const result = await planner.executePlan(input.planId, input.options);

        return {
          success: result.success,
          completedSteps: result.completedSteps.map((step: any) => ({
            id: step.id,
            name: step.name,
            status: step.status,
          })),
          errors: result.errors,
        };
      } catch (error) {
        throw createErrorHandler('Execute task plan')(error);
      }
    }),

  // Get task plan status
  getTaskPlanStatus: protectedProcedure
    .input(z.object({ planId: z.string() }))
    .output(z.object({
      plan: taskPlanSchema.optional(),
      progress: z.number().min(0).max(1),
      nextStep: z.object({
        id: z.string(),
        name: z.string(),
        description: z.string(),
        estimatedTime: z.number(),
      }).optional(),
      blockedSteps: z.array(z.object({
        id: z.string(),
        name: z.string(),
        status: z.string(),
      })),
    }))
    .query(async ({ input }) => {
      try {
        const { PlannerAgent, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const planner = new PlannerAgent(vertexAI);

        const status = planner.getPlanStatus(input.planId);

        return {
          plan: status.plan ? {
            id: status.plan.id,
            title: status.plan.title,
            description: status.plan.description,
            rtfStructure: status.plan.rtfStructure,
            sowDocument: status.plan.sowDocument,
            executionPlan: status.plan.executionPlan,
            status: status.plan.status,
            createdAt: status.plan.createdAt,
            updatedAt: status.plan.updatedAt,
          } : undefined,
          progress: status.progress,
          nextStep: status.nextStep ? {
            id: status.nextStep.id,
            name: status.nextStep.name,
            description: status.nextStep.description,
            estimatedTime: status.nextStep.estimatedTime,
          } : undefined,
          blockedSteps: status.blockedSteps.map((step: any) => ({
            id: step.id,
            name: step.name,
            status: step.status,
          })),
        };
      } catch (error) {
        throw createErrorHandler('Get task plan status')(error);
      }
    }),

  // Get all active task plans
  getActiveTaskPlans: protectedProcedure
    .output(z.array(taskPlanSchema))
    .query(async () => {
      try {
        const { PlannerAgent, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const planner = new PlannerAgent(vertexAI);

        const activePlans = planner.getActivePlans();

        return activePlans.map((plan: any) => ({
          id: plan.id,
          title: plan.title,
          description: plan.description,
          rtfStructure: plan.rtfStructure,
          sowDocument: plan.sowDocument,
          executionPlan: plan.executionPlan,
          status: plan.status,
          createdAt: plan.createdAt,
          updatedAt: plan.updatedAt,
        }));
      } catch (error) {
        throw createErrorHandler('Get active task plans')(error);
      }
    }),

  // Generate SOW document for a task plan
  generateSOW: protectedProcedure
    .use(rateLimit(10, 60 * 60 * 1000)) // 10 SOW generations per hour
    .input(generateSOWSchema)
    .output(z.record(z.any()))
    .mutation(async ({ input }) => {
      try {
        const { PlannerAgent, SOWGenerator, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const planner = new PlannerAgent(vertexAI);
        const sowGenerator = new SOWGenerator(vertexAI);

        // Get the task plan
        const status = planner.getPlanStatus(input.planId);
        if (!status.plan) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Task plan not found',
          });
        }

        // Generate SOW document
        const sowDocument = await sowGenerator.generateSOW(
          status.plan.rtfStructure,
          input.options || {}
        );

        return sowDocument;
      } catch (error) {
        throw createErrorHandler('Generate SOW')(error);
      }
    }),

  // Get conversation context
  getConversationContext: protectedProcedure
    .input(z.object({ sessionId: z.string() }))
    .output(z.object({
      context: z.record(z.any()).optional(),
      stats: z.object({
        activeConversations: z.number(),
        conversationsByType: z.record(z.number()),
        conversationsByPhase: z.record(z.number()),
      }),
    }))
    .query(async ({ input }) => {
      try {
        const { PlannerAgent, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const planner = new PlannerAgent(vertexAI);

        const context = planner.getConversationContext(input.sessionId);
        
        // Get conversation manager stats
        const conversationManager = (planner as any).conversationManager;
        const stats = conversationManager.getStats();

        return {
          context: context ? {
            type: context.type,
            id: context.id,
            metadata: context.metadata,
            history: context.history,
            state: context.state,
          } : undefined,
          stats,
        };
      } catch (error) {
        throw createErrorHandler('Get conversation context')(error);
      }
    }),

  // Analyze effort estimation
  analyzeEffort: protectedProcedure
    .input(z.object({
      task: z.string().min(1).max(1000),
      complexity: z.enum(['simple', 'moderate', 'complex', 'very-complex']),
      context: z.object({
        type: z.string(),
        requirements: z.array(z.string()),
        constraints: z.array(z.string()),
      }),
    }))
    .output(z.object({
      optimistic: z.number(),
      mostLikely: z.number(),
      pessimistic: z.number(),
      expected: z.number(),
      confidence: z.enum(['high', 'medium', 'low']),
    }))
    .mutation(async ({ input }) => {
      try {
        const { SOWGenerator, createVertexAIClient } = await import('@maria/ai-agents');
        
        const vertexAI = createVertexAIClient();
        const sowGenerator = new SOWGenerator(vertexAI);

        const effortEstimate = await sowGenerator.generateEffortEstimate(
          input.task,
          input.complexity,
          input.context
        );

        return {
          optimistic: effortEstimate.optimistic,
          mostLikely: effortEstimate.mostLikely,
          pessimistic: effortEstimate.pessimistic,
          expected: effortEstimate.expected,
          confidence: effortEstimate.confidence,
        };
      } catch (error) {
        throw createErrorHandler('Analyze effort')(error);
      }
    }),
});