/**
 * AI Execution Engine
 * Handles AI model integration with Vertex AI and xAI
 */

import { z } from 'zod';
import { type UserSession } from './maria-auth';

// AI Model schemas
export const AIModelSchema = z.enum([
  'gemini-2.5-pro-preview',
  'grok-4-latest',
]);

export type AIModel = z.infer<typeof AIModelSchema>;

export const AIRequestSchema = z.object({
  model: AIModelSchema,
  prompt: z.string(),
  context: z.string().optional(),
  temperature: z.number().min(0).max(2).default(0.7),
  maxTokens: z.number().min(1).max(8192).default(2048),
  stream: z.boolean().default(false),
  systemPrompt: z.string().optional(),
});

export type AIRequest = z.infer<typeof AIRequestSchema>;

export const AIResponseSchema = z.object({
  id: z.string(),
  model: AIModelSchema,
  content: z.string(),
  usage: z.object({
    promptTokens: z.number(),
    completionTokens: z.number(),
    totalTokens: z.number(),
  }),
  finishReason: z.enum(['stop', 'length', 'content_filter', 'function_call']),
  processingTimeMs: z.number(),
  cached: z.boolean().default(false),
});

export type AIResponse = z.infer<typeof AIResponseSchema>;

// Response cache for optimization
class ResponseCache {
  private cache = new Map<string, { response: AIResponse; timestamp: number }>();
  private maxAge = 10 * 60 * 1000; // 10 minutes
  private maxSize = 1000;

  generateKey(request: AIRequest): string {
    return `${request.model}:${this.hashString(request.prompt + (request.context || '') + (request.systemPrompt || ''))}:${request.temperature}:${request.maxTokens}`;
  }

  get(key: string): AIResponse | null {
    const cached = this.cache.get(key);
    if (!cached) return null;
    
    if (Date.now() - cached.timestamp > this.maxAge) {
      this.cache.delete(key);
      return null;
    }
    
    return { ...cached.response, cached: true };
  }

  set(key: string, response: AIResponse): void {
    // Remove oldest entries if cache is full
    if (this.cache.size >= this.maxSize) {
      const oldestKey = this.cache.keys().next().value;
      if (oldestKey) {
        this.cache.delete(oldestKey);
      }
    }
    
    this.cache.set(key, {
      response: { ...response, cached: false },
      timestamp: Date.now(),
    });
  }

  private hashString(str: string): string {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      const char = str.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convert to 32-bit integer
    }
    return hash.toString(36);
  }

  clear(): void {
    this.cache.clear();
  }

  getStats(): { size: number; maxSize: number; hitRate: number } {
    return {
      size: this.cache.size,
      maxSize: this.maxSize,
      hitRate: 0, // TODO: Track hit rate
    };
  }
}

// AI Execution Engine class
export class AIExecutionEngine {
  private grokBaseUrl = 'https://api.x.ai/v1';
  private cache = new ResponseCache();
  private requestCount = 0;
  private errorCount = 0;

  constructor() {
    // Use Vertex AI through ai-agents package
  }

  // Main execution method
  async execute(request: AIRequest, user?: UserSession): Promise<AIResponse> {
    const startTime = Date.now();
    this.requestCount++;

    try {
      // Check cache first
      const cacheKey = this.cache.generateKey(request);
      const cachedResponse = this.cache.get(cacheKey);
      if (cachedResponse) {
        return cachedResponse;
      }

      // Check user permissions for model access
      if (user && !this.hasModelAccess(user, request.model)) {
        throw new Error(`Access denied to model ${request.model} for plan ${user.plan.type}`);
      }

      // Route to appropriate model
      let response: AIResponse;
      switch (request.model) {
        case 'gemini-2.5-pro-preview':
          response = await this.executeGemini(request);
          break;
        case 'grok-4-latest':
          response = await this.executeGrok(request);
          break;
        default:
          throw new Error(`Unsupported model: ${request.model}`);
      }

      // Calculate processing time
      response.processingTimeMs = Date.now() - startTime;

      // Cache the response
      this.cache.set(cacheKey, response);

      return response;
    } catch (error) {
      this.errorCount++;
      throw new Error(`AI execution failed: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  // Gemini execution (TODO: Implement with Vertex AI through ai-agents)
  private async executeGemini(request: AIRequest): Promise<AIResponse> {
    // TODO: Use Vertex AI through ai-agents package
    return {
      id: this.generateId(),
      model: request.model,
      content: 'Gemini execution not yet implemented - use ai-agents package',
      usage: {
        promptTokens: 0,
        completionTokens: 0,
        totalTokens: 0,
      },
      finishReason: 'stop',
      processingTimeMs: 0,
      cached: false,
    };
  }

  // Grok execution (xAI API)
  private async executeGrok(request: AIRequest): Promise<AIResponse> {
    const grokApiKey = process.env.GROK_API_KEY;
    if (!grokApiKey) {
      throw new Error('GROK_API_KEY not found in environment variables');
    }

    try {
      const messages = [];
      
      if (request.systemPrompt) {
        messages.push({
          role: 'system',
          content: request.systemPrompt,
        });
      }
      
      if (request.context) {
        messages.push({
          role: 'user',
          content: `Context: ${request.context}`,
        });
      }
      
      messages.push({
        role: 'user',
        content: request.prompt,
      });

      const response = await fetch(`${this.grokBaseUrl}/chat/completions`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${grokApiKey}`,
        },
        body: JSON.stringify({
          model: 'grok-beta',
          messages,
          temperature: request.temperature,
          max_tokens: request.maxTokens,
          stream: request.stream,
        }),
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`Grok API error: ${response.status} - ${errorText}`);
      }

      const data = await response.json();
      const choice = data.choices?.[0];
      
      if (!choice) {
        throw new Error('No response from Grok API');
      }

      return {
        id: data.id || this.generateId(),
        model: request.model,
        content: choice.message.content,
        usage: {
          promptTokens: data.usage?.prompt_tokens || 0,
          completionTokens: data.usage?.completion_tokens || 0,
          totalTokens: data.usage?.total_tokens || 0,
        },
        finishReason: choice.finish_reason === 'stop' ? 'stop' : 'length',
        processingTimeMs: 0, // Will be set by caller
        cached: false,
      };
    } catch (error) {
      throw new Error(`Grok execution failed: ${error instanceof Error ? error.message : 'Unknown error'}`);
    }
  }

  // Check if user has access to model based on their plan
  private hasModelAccess(user: UserSession, model: AIModel): boolean {
    const allowedModels = user.plan.limits.aiModelAccess;
    return allowedModels.includes(model);
  }


  // Generate unique ID
  private generateId(): string {
    return `ai-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
  }

  // Streaming support for real-time responses
  async *executeStream(request: AIRequest, user?: UserSession): AsyncGenerator<string, void, unknown> {
    if (request.model === 'grok-4-latest') {
      yield* this.executeGrokStream(request, user);
    } else {
      // For non-streaming models, yield the complete response
      const response = await this.execute(request, user);
      yield response.content;
    }
  }

  private async *executeGrokStream(request: AIRequest, user?: UserSession): AsyncGenerator<string, void, unknown> {
    const grokApiKey = process.env.GROK_API_KEY;
    if (!grokApiKey) {
      throw new Error('GROK_API_KEY not found in environment variables');
    }

    if (user && !this.hasModelAccess(user, request.model)) {
      throw new Error(`Access denied to model ${request.model} for plan ${user.plan.type}`);
    }

    const messages = [];
    
    if (request.systemPrompt) {
      messages.push({
        role: 'system',
        content: request.systemPrompt,
      });
    }
    
    if (request.context) {
      messages.push({
        role: 'user',
        content: `Context: ${request.context}`,
      });
    }
    
    messages.push({
      role: 'user',
      content: request.prompt,
    });

    const response = await fetch(`${this.grokBaseUrl}/chat/completions`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${grokApiKey}`,
      },
      body: JSON.stringify({
        model: 'grok-beta',
        messages,
        temperature: request.temperature,
        max_tokens: request.maxTokens,
        stream: true,
      }),
    });

    if (!response.ok) {
      throw new Error(`Grok API error: ${response.status}`);
    }

    const reader = response.body?.getReader();
    if (!reader) {
      throw new Error('No response body reader available');
    }

    const decoder = new TextDecoder();
    
    try {
      while (true) {
        const { done, value } = await reader.read();
        if (done) break;
        
        const chunk = decoder.decode(value);
        const lines = chunk.split('\n');
        
        for (const line of lines) {
          if (line.startsWith('data: ')) {
            const data = line.slice(6);
            if (data === '[DONE]') {
              return;
            }
            
            try {
              const parsed = JSON.parse(data);
              const content = parsed.choices?.[0]?.delta?.content;
              if (content) {
                yield content;
              }
            } catch {
              // Skip invalid JSON lines
            }
          }
        }
      }
    } finally {
      reader.releaseLock();
    }
  }

  // Get execution statistics
  getStats(): {
    requestCount: number;
    errorCount: number;
    successRate: number;
    cacheStats: ReturnType<ResponseCache['getStats']>;
  } {
    return {
      requestCount: this.requestCount,
      errorCount: this.errorCount,
      successRate: this.requestCount > 0 ? (this.requestCount - this.errorCount) / this.requestCount : 0,
      cacheStats: this.cache.getStats(),
    };
  }

  // Clear cache
  clearCache(): void {
    this.cache.clear();
  }

  // Health check
  async healthCheck(): Promise<{ gemini: boolean; grok: boolean }> {
    const results = { gemini: false, grok: false };

    // Test Gemini
    try {
      await this.executeGemini({
        model: 'gemini-2.5-pro-preview',
        prompt: 'Hello',
        temperature: 0.1,
        maxTokens: 10,
        stream: false,
      });
      results.gemini = true;
    } catch (error) {
      console.warn('Gemini health check failed:', error);
    }

    // Test Grok
    try {
      await this.executeGrok({
        model: 'grok-4-latest',
        prompt: 'Hello',
        temperature: 0.1,
        maxTokens: 10,
        stream: false,
      });
      results.grok = true;
    } catch (error) {
      console.warn('Grok health check failed:', error);
    }

    return results;
  }
}

// Export singleton instance
export const aiExecutionEngine = new AIExecutionEngine();