/**
 * MARIA Auth Service
 * Plan-based membership system with authentication
 */

import { z } from 'zod';
import bcrypt from 'bcrypt';
import { randomBytes } from 'crypto';

// Plan schemas
export const PlanType = z.enum(['free', 'pro', 'max']);
export type PlanType = z.infer<typeof PlanType>;

export const UserPlanSchema = z.object({
  type: PlanType,
  limits: z.object({
    dailyRequests: z.number(),
    monthlyRequests: z.number(),
    maxProjects: z.number(),
    maxCollaborators: z.number(),
    cpuLimitCores: z.number(),
    memoryLimitMB: z.number(),
    storageLimitMB: z.number(),
    aiModelAccess: z.array(z.string()),
  }),
  features: z.object({
    priority: z.enum(['low', 'medium', 'high']),
    support: z.enum(['community', 'email', '24h']),
    analytics: z.boolean(),
    customBranding: z.boolean(),
    apiAccess: z.boolean(),
    advancedIntegrations: z.boolean(),
  }),
});

export type UserPlan = z.infer<typeof UserPlanSchema>;

// User auth schemas
export const CreateUserSchema = z.object({
  email: z.string().email(),
  password: z.string().min(8),
  name: z.string().min(1),
  plan: PlanType.optional().default('free'),
});

export const LoginSchema = z.object({
  email: z.string().email(),
  password: z.string(),
});

export const UserSessionSchema = z.object({
  uid: z.string(),
  email: z.string(),
  name: z.string(),
  plan: UserPlanSchema,
  usage: z.object({
    dailyRequests: z.number(),
    monthlyRequests: z.number(),
    lastReset: z.string(), // ISO date
  }),
  createdAt: z.string(),
  lastLoginAt: z.string(),
  isEmailVerified: z.boolean(),
  mfaEnabled: z.boolean(),
});

export type UserSession = z.infer<typeof UserSessionSchema>;

// Plan definitions
export const PLAN_DEFINITIONS: Record<PlanType, UserPlan> = {
  free: {
    type: 'free',
    limits: {
      dailyRequests: 50,
      monthlyRequests: 1000,
      maxProjects: 3,
      maxCollaborators: 2,
      cpuLimitCores: 0.5,
      memoryLimitMB: 512,
      storageLimitMB: 100,
      aiModelAccess: ['gemini-2.5-pro-preview'],
    },
    features: {
      priority: 'low',
      support: 'community',
      analytics: false,
      customBranding: false,
      apiAccess: false,
      advancedIntegrations: false,
    },
  },
  pro: {
    type: 'pro',
    limits: {
      dailyRequests: 500,
      monthlyRequests: 10000,
      maxProjects: 25,
      maxCollaborators: 10,
      cpuLimitCores: 2,
      memoryLimitMB: 2048,
      storageLimitMB: 1000,
      aiModelAccess: ['gemini-2.5-pro-preview', 'grok-4-latest'],
    },
    features: {
      priority: 'medium',
      support: 'email',
      analytics: true,
      customBranding: false,
      apiAccess: true,
      advancedIntegrations: true,
    },
  },
  max: {
    type: 'max',
    limits: {
      dailyRequests: 2000,
      monthlyRequests: 50000,
      maxProjects: 100,
      maxCollaborators: 50,
      cpuLimitCores: 8,
      memoryLimitMB: 8192,
      storageLimitMB: 10000,
      aiModelAccess: ['gemini-2.5-pro-preview', 'grok-4-latest'],
    },
    features: {
      priority: 'high',
      support: '24h',
      analytics: true,
      customBranding: true,
      apiAccess: true,
      advancedIntegrations: true,
    },
  },
};

// In-memory storage for development (replace with Firestore in production)
class AuthStore {
  private users: Map<string, any> = new Map();
  private sessions: Map<string, UserSession> = new Map();

  // User management
  async createUser(data: z.infer<typeof CreateUserSchema>) {
    const uid = this.generateId();
    const hashedPassword = await bcrypt.hash(data.password, 12);
    
    const user = {
      uid,
      email: data.email.toLowerCase(),
      name: data.name,
      password: hashedPassword,
      plan: PLAN_DEFINITIONS[data.plan],
      usage: {
        dailyRequests: 0,
        monthlyRequests: 0,
        lastReset: new Date().toISOString(),
      },
      createdAt: new Date().toISOString(),
      lastLoginAt: new Date().toISOString(),
      isEmailVerified: false,
      mfaEnabled: false,
    };

    this.users.set(uid, user);
    return { uid, email: user.email, name: user.name };
  }

  async getUserByEmail(email: string) {
    for (const [uid, user] of this.users) {
      if (user.email === email.toLowerCase()) {
        return { uid, ...user };
      }
    }
    return null;
  }

  async getUserById(uid: string) {
    return this.users.get(uid) || null;
  }

  async updateUser(uid: string, updates: Partial<any>) {
    const user = this.users.get(uid);
    if (!user) return null;
    
    const updatedUser = { ...user, ...updates };
    this.users.set(uid, updatedUser);
    return updatedUser;
  }

  // Session management
  async createSession(uid: string): Promise<string> {
    const user = await this.getUserById(uid);
    if (!user) throw new Error('User not found');

    const sessionToken = this.generateToken();
    const session: UserSession = {
      uid: user.uid,
      email: user.email,
      name: user.name,
      plan: user.plan,
      usage: user.usage,
      createdAt: user.createdAt,
      lastLoginAt: new Date().toISOString(),
      isEmailVerified: user.isEmailVerified,
      mfaEnabled: user.mfaEnabled,
    };

    this.sessions.set(sessionToken, session);
    
    // Update last login
    await this.updateUser(uid, { lastLoginAt: session.lastLoginAt });
    
    return sessionToken;
  }

  async getSession(token: string): Promise<UserSession | null> {
    return this.sessions.get(token) || null;
  }

  async destroySession(token: string): Promise<boolean> {
    return this.sessions.delete(token);
  }

  // Usage tracking
  async incrementUsage(uid: string, type: 'daily' | 'monthly' = 'daily') {
    const user = await this.getUserById(uid);
    if (!user) return null;

    const now = new Date();
    const lastReset = new Date(user.usage.lastReset);
    
    // Reset daily counter if it's a new day
    if (type === 'daily' && now.getDate() !== lastReset.getDate()) {
      user.usage.dailyRequests = 0;
    }
    
    // Reset monthly counter if it's a new month
    if (type === 'monthly' && now.getMonth() !== lastReset.getMonth()) {
      user.usage.monthlyRequests = 0;
    }

    user.usage.dailyRequests += 1;
    user.usage.monthlyRequests += 1;
    user.usage.lastReset = now.toISOString();

    await this.updateUser(uid, { usage: user.usage });
    return user.usage;
  }

  // Utility methods
  private generateId(): string {
    return randomBytes(16).toString('hex');
  }

  private generateToken(): string {
    return randomBytes(32).toString('hex');
  }

  // Development helper: get all users
  getAllUsers() {
    return Array.from(this.users.entries()).map(([uid, user]) => ({
      uid,
      email: user.email,
      name: user.name,
      plan: user.plan.type,
      usage: user.usage,
      createdAt: user.createdAt,
      lastLoginAt: user.lastLoginAt,
    }));
  }
}

// Global auth store instance
export const authStore = new AuthStore();

// Auth service class
export class MariaAuthService {
  // User registration
  async register(data: z.infer<typeof CreateUserSchema>) {
    // Check if user already exists
    const existingUser = await authStore.getUserByEmail(data.email);
    if (existingUser) {
      throw new Error('User already exists with this email');
    }

    // Create user
    const user = await authStore.createUser(data);
    
    // Create session
    const sessionToken = await authStore.createSession(user.uid);
    
    return {
      user,
      sessionToken,
      message: 'User registered successfully',
    };
  }

  // User login
  async login(data: z.infer<typeof LoginSchema>) {
    const user = await authStore.getUserByEmail(data.email);
    if (!user) {
      throw new Error('Invalid email or password');
    }

    // Verify password
    const isValid = await bcrypt.compare(data.password, user.password);
    if (!isValid) {
      throw new Error('Invalid email or password');
    }

    // Create session
    const sessionToken = await authStore.createSession(user.uid);
    
    return {
      user: {
        uid: user.uid,
        email: user.email,
        name: user.name,
        plan: user.plan.type,
      },
      sessionToken,
      message: 'Login successful',
    };
  }

  // Get user session
  async getSession(token: string) {
    const session = await authStore.getSession(token);
    if (!session) {
      throw new Error('Invalid or expired session');
    }
    return session;
  }

  // Logout
  async logout(token: string) {
    const destroyed = await authStore.destroySession(token);
    return {
      success: destroyed,
      message: destroyed ? 'Logout successful' : 'Session not found',
    };
  }

  // Check resource limits
  async checkLimits(uid: string, resource: keyof UserPlan['limits']) {
    const user = await authStore.getUserById(uid);
    if (!user) throw new Error('User not found');

    const limit = user.plan.limits[resource];
    const usage = user.usage;

    switch (resource) {
      case 'dailyRequests':
        return usage.dailyRequests < limit;
      case 'monthlyRequests':
        return usage.monthlyRequests < limit;
      default:
        return true;
    }
  }

  // Increment usage
  async trackUsage(uid: string) {
    return await authStore.incrementUsage(uid);
  }

  // Get plan info
  getPlanInfo(planType: PlanType) {
    return PLAN_DEFINITIONS[planType];
  }

  // Development helper
  getAllUsers() {
    return authStore.getAllUsers();
  }
}

// Export singleton instance
export const mariaAuth = new MariaAuthService();