/**
 * Sandbox API Router
 * Handles sandbox orchestration and management
 */

import { z } from 'zod';
import { router, publicProcedure } from '../trpc';
import { TRPCError } from '@trpc/server';
import { mariaAuth } from '../lib/maria-auth';
import { sandboxOrchestrator } from '../lib/sandbox-orchestrator';

// Session token validation middleware for sandbox operations
const withAuth = publicProcedure.use(async ({ next, ctx }) => {
  const token = ctx.req?.headers?.authorization?.replace('Bearer ', '');
  
  if (!token) {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Authentication token required',
    });
  }

  try {
    const session = await mariaAuth.getSession(token);
    return next({
      ctx: {
        ...ctx,
        session,
        userId: session.uid,
      },
    });
  } catch {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Invalid or expired session',
    });
  }
});

export const sandboxRouter = router({
  // Get or create user sandbox
  getOrCreate: withAuth.mutation(async ({ ctx }) => {
    try {
      const sandbox = await sandboxOrchestrator.getOrCreateSandbox(ctx.userId);
      return {
        success: true,
        data: sandbox,
      };
    } catch (error) {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: error instanceof Error ? error.message : 'Failed to create sandbox',
      });
    }
  }),

  // Get sandbox status
  getStatus: withAuth
    .input(z.object({ sandboxId: z.string().optional() }))
    .query(async ({ input, ctx }) => {
      try {
        let sandbox;
        
        if (input.sandboxId) {
          sandbox = await sandboxOrchestrator.getSandboxStatus(input.sandboxId);
        } else {
          // Get user's current sandbox
          sandbox = await sandboxOrchestrator.getOrCreateSandbox(ctx.userId);
        }

        if (!sandbox) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Sandbox not found',
          });
        }

        // Only allow users to see their own sandboxes
        if (sandbox.userId !== ctx.userId) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied to this sandbox',
          });
        }

        return {
          success: true,
          data: sandbox,
        };
      } catch (error) {
        if (error instanceof TRPCError) throw error;
        
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to get sandbox status',
        });
      }
    }),

  // Stop sandbox
  stop: withAuth
    .input(z.object({ sandboxId: z.string() }))
    .mutation(async ({ input, ctx }) => {
      try {
        const sandbox = await sandboxOrchestrator.getSandboxStatus(input.sandboxId);
        
        if (!sandbox) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Sandbox not found',
          });
        }

        // Only allow users to stop their own sandboxes
        if (sandbox.userId !== ctx.userId) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied to this sandbox',
          });
        }

        const stopped = await sandboxOrchestrator.stopSandbox(input.sandboxId);
        
        return {
          success: stopped,
          message: stopped ? 'Sandbox stopped successfully' : 'Failed to stop sandbox',
        };
      } catch (error) {
        if (error instanceof TRPCError) throw error;
        
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to stop sandbox',
        });
      }
    }),

  // Update activity (keep sandbox alive)
  updateActivity: withAuth
    .input(z.object({ sandboxId: z.string() }))
    .mutation(async ({ input, ctx }) => {
      try {
        const sandbox = await sandboxOrchestrator.getSandboxStatus(input.sandboxId);
        
        if (!sandbox) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Sandbox not found',
          });
        }

        // Only allow users to update their own sandboxes
        if (sandbox.userId !== ctx.userId) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied to this sandbox',
          });
        }

        const updated = await sandboxOrchestrator.updateActivity(input.sandboxId);
        
        return {
          success: updated,
          message: updated ? 'Activity updated' : 'Failed to update activity',
        };
      } catch (error) {
        if (error instanceof TRPCError) throw error;
        
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to update activity',
        });
      }
    }),

  // Get sandbox metrics
  getMetrics: withAuth
    .input(z.object({ sandboxId: z.string() }))
    .query(async ({ input, ctx }) => {
      try {
        const sandbox = await sandboxOrchestrator.getSandboxStatus(input.sandboxId);
        
        if (!sandbox) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Sandbox not found',
          });
        }

        // Only allow users to see their own sandbox metrics
        if (sandbox.userId !== ctx.userId) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied to this sandbox',
          });
        }

        const metrics = await sandboxOrchestrator.getMetrics(input.sandboxId);
        
        return {
          success: true,
          data: {
            sandbox,
            metrics,
            cost: sandboxOrchestrator.calculateCost(sandbox),
          },
        };
      } catch (error) {
        if (error instanceof TRPCError) throw error;
        
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to get metrics',
        });
      }
    }),

  // Admin endpoints (for development and monitoring)
  admin: router({
    // List all sandboxes
    listAll: withAuth.query(async ({ ctx }) => {
      // Simple admin check - in production, this should be more robust
      if (!ctx.session.email.includes('admin') && !ctx.session.email.includes('bonginkan')) {
        throw new TRPCError({
          code: 'FORBIDDEN',
          message: 'Admin access required',
        });
      }

      try {
        const sandboxes = await sandboxOrchestrator.listAllSandboxes();
        
        return {
          success: true,
          data: sandboxes.map(sandbox => ({
            ...sandbox,
            cost: sandboxOrchestrator.calculateCost(sandbox),
          })),
        };
      } catch {
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to list sandboxes',
        });
      }
    }),

    // Get resource usage summary
    getResourceSummary: withAuth.query(async ({ ctx }) => {
      // Simple admin check
      if (!ctx.session.email.includes('admin') && !ctx.session.email.includes('bonginkan')) {
        throw new TRPCError({
          code: 'FORBIDDEN',
          message: 'Admin access required',
        });
      }

      try {
        const sandboxes = await sandboxOrchestrator.listAllSandboxes();
        
        const summary = {
          total: sandboxes.length,
          byStatus: {} as Record<string, number>,
          totalCost: 0,
          totalResources: {
            cpuCores: 0,
            memoryMB: 0,
            storageMB: 0,
          },
        };

        sandboxes.forEach(sandbox => {
          // Count by status
          summary.byStatus[sandbox.status] = (summary.byStatus[sandbox.status] || 0) + 1;
          
          // Sum costs
          summary.totalCost += sandboxOrchestrator.calculateCost(sandbox);
          
          // Sum resources for active sandboxes
          if (['ready', 'running'].includes(sandbox.status)) {
            summary.totalResources.cpuCores += sandbox.resources.cpuCores;
            summary.totalResources.memoryMB += sandbox.resources.memoryMB;
            summary.totalResources.storageMB += sandbox.resources.storageMB;
          }
        });

        return {
          success: true,
          data: summary,
        };
      } catch {
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to get resource summary',
        });
      }
    }),
  }),

  // Development endpoints
  dev: router({
    // Create test sandboxes
    createTestSandboxes: publicProcedure.mutation(async () => {
      try {
        const testUsers = ['free-user-123', 'pro-user-456', 'max-user-789'];
        const results = [];

        for (const userId of testUsers) {
          try {
            const sandbox = await sandboxOrchestrator.getOrCreateSandbox(userId);
            results.push({
              userId,
              sandboxId: sandbox.id,
              status: 'created',
              resources: sandbox.resources,
            });
          } catch (error) {
            results.push({
              userId,
              status: 'error',
              error: error instanceof Error ? error.message : 'Unknown error',
            });
          }
        }

        return {
          success: true,
          data: results,
        };
      } catch {
        throw new TRPCError({
          code: 'INTERNAL_SERVER_ERROR',
          message: 'Failed to create test sandboxes',
        });
      }
    }),
  }),
});