/**
 * Sandbox Orchestrator
 * Manages Cloud Run sandbox environments for MARIA users
 */

import { z } from 'zod';
import { mariaAuth } from './maria-auth';

// Sandbox resource schemas
export const SandboxResourcesSchema = z.object({
  cpuCores: z.number(),
  memoryMB: z.number(),
  storageMB: z.number(),
  gpuType: z.enum(['none', 'nvidia-t4', 'nvidia-v100']).optional(),
  timeoutMinutes: z.number().default(30),
});

export type SandboxResources = z.infer<typeof SandboxResourcesSchema>;

export const SandboxStatusSchema = z.enum([
  'creating',
  'starting',
  'ready',
  'running',
  'stopping',
  'stopped',
  'error',
  'timeout'
]);

export type SandboxStatus = z.infer<typeof SandboxStatusSchema>;

export const SandboxInstanceSchema = z.object({
  id: z.string(),
  userId: z.string(),
  status: SandboxStatusSchema,
  resources: SandboxResourcesSchema,
  createdAt: z.string(),
  startedAt: z.string().optional(),
  lastActivityAt: z.string(),
  endpointUrl: z.string().optional(),
  containerImage: z.string(),
  environment: z.record(z.string()),
  metrics: z.object({
    cpuUsagePercent: z.number(),
    memoryUsageMB: z.number(),
    storageUsageMB: z.number(),
    networkInMB: z.number(),
    networkOutMB: z.number(),
  }).optional(),
});

export type SandboxInstance = z.infer<typeof SandboxInstanceSchema>;

// Resource allocation based on user plan
const PLAN_RESOURCES = {
  free: {
    cpuCores: 0.5,
    memoryMB: 512,
    storageMB: 1024,
    timeoutMinutes: 30,
  },
  pro: {
    cpuCores: 2,
    memoryMB: 2048,
    storageMB: 4096,
    gpuType: 'nvidia-t4' as const,
    timeoutMinutes: 60,
  },
  max: {
    cpuCores: 8,
    memoryMB: 8192,
    storageMB: 16384,
    gpuType: 'nvidia-v100' as const,
    timeoutMinutes: 120,
  },
} as const;

// In-memory storage for development (replace with Cloud Storage/Firestore in production)
class SandboxStore {
  private instances: Map<string, SandboxInstance> = new Map();
  private userSandboxes: Map<string, string> = new Map(); // userId -> sandboxId

  async createInstance(userId: string, resources: SandboxResources): Promise<string> {
    const sandboxId = this.generateId();
    const now = new Date().toISOString();
    
    const instance: SandboxInstance = {
      id: sandboxId,
      userId,
      status: 'creating',
      resources,
      createdAt: now,
      lastActivityAt: now,
      containerImage: 'gcr.io/maria-code/user-sandbox:latest',
      environment: {
        MARIA_USER_ID: userId,
        MARIA_SANDBOX_ID: sandboxId,
        NODE_ENV: 'sandbox',
      },
    };

    this.instances.set(sandboxId, instance);
    this.userSandboxes.set(userId, sandboxId);
    
    return sandboxId;
  }

  async getInstance(sandboxId: string): Promise<SandboxInstance | null> {
    return this.instances.get(sandboxId) || null;
  }

  async getUserSandbox(userId: string): Promise<SandboxInstance | null> {
    const sandboxId = this.userSandboxes.get(userId);
    if (!sandboxId) return null;
    return this.getInstance(sandboxId);
  }

  async updateInstance(sandboxId: string, updates: Partial<SandboxInstance>): Promise<boolean> {
    const instance = this.instances.get(sandboxId);
    if (!instance) return false;
    
    const updatedInstance = { ...instance, ...updates };
    this.instances.set(sandboxId, updatedInstance);
    return true;
  }

  async deleteInstance(sandboxId: string): Promise<boolean> {
    const instance = this.instances.get(sandboxId);
    if (!instance) return false;
    
    this.instances.delete(sandboxId);
    this.userSandboxes.delete(instance.userId);
    return true;
  }

  async getAllInstances(): Promise<SandboxInstance[]> {
    return Array.from(this.instances.values());
  }

  async getInstancesByStatus(status: SandboxStatus): Promise<SandboxInstance[]> {
    return Array.from(this.instances.values()).filter(i => i.status === status);
  }

  private generateId(): string {
    return `sandbox-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
  }
}

// Global store instance
const sandboxStore = new SandboxStore();

// Sandbox Orchestrator class
export class SandboxOrchestrator {
  private cleanupInterval: NodeJS.Timeout;

  constructor() {
    // Start cleanup process for idle sandboxes
    this.cleanupInterval = setInterval(() => {
      this.cleanupIdleSandboxes();
    }, 5 * 60 * 1000); // Check every 5 minutes
  }

  // Create or get user sandbox
  async getOrCreateSandbox(userId: string): Promise<SandboxInstance> {
    // Check if user already has an active sandbox
    let sandbox = await sandboxStore.getUserSandbox(userId);
    
    if (sandbox && ['ready', 'running'].includes(sandbox.status)) {
      // Update last activity
      await sandboxStore.updateInstance(sandbox.id, {
        lastActivityAt: new Date().toISOString(),
      });
      return sandbox;
    }

    // Get user session to determine plan
    let userPlan = 'free';
    try {
      // In a real implementation, we'd get this from a valid session
      const allUsers = mariaAuth.getAllUsers();
      const user = allUsers.find(u => u.uid === userId);
      if (user) {
        userPlan = user.plan;
      }
    } catch {
      console.warn('Could not determine user plan, defaulting to free');
    }

    // Get resources for user plan
    let resources: SandboxResources;
    if (userPlan === 'free' || userPlan === 'pro' || userPlan === 'max') {
      resources = PLAN_RESOURCES[userPlan];
    } else {
      resources = PLAN_RESOURCES.free;
    }

    // Create new sandbox
    const sandboxId = await sandboxStore.createInstance(userId, resources);
    
    // Simulate sandbox creation process
    await this.createCloudRunSandbox(sandboxId);
    
    const newSandbox = await sandboxStore.getInstance(sandboxId);
    if (!newSandbox) {
      throw new Error('Failed to create sandbox instance');
    }

    return newSandbox;
  }

  // Get sandbox status
  async getSandboxStatus(sandboxId: string): Promise<SandboxInstance | null> {
    return await sandboxStore.getInstance(sandboxId);
  }

  // Stop user sandbox
  async stopSandbox(sandboxId: string): Promise<boolean> {
    const sandbox = await sandboxStore.getInstance(sandboxId);
    if (!sandbox) return false;

    await sandboxStore.updateInstance(sandboxId, {
      status: 'stopping',
      lastActivityAt: new Date().toISOString(),
    });

    // Simulate stopping process
    await this.stopCloudRunSandbox(sandboxId);

    await sandboxStore.updateInstance(sandboxId, {
      status: 'stopped',
    });

    return true;
  }

  // Update sandbox activity
  async updateActivity(sandboxId: string): Promise<boolean> {
    return await sandboxStore.updateInstance(sandboxId, {
      lastActivityAt: new Date().toISOString(),
    });
  }

  // Get resource usage metrics
  async getMetrics(sandboxId: string): Promise<SandboxInstance['metrics'] | null> {
    const sandbox = await sandboxStore.getInstance(sandboxId);
    if (!sandbox) return null;

    // Simulate metrics collection
    const metrics = {
      cpuUsagePercent: Math.random() * 100,
      memoryUsageMB: Math.random() * sandbox.resources.memoryMB,
      storageUsageMB: Math.random() * sandbox.resources.storageMB,
      networkInMB: Math.random() * 100,
      networkOutMB: Math.random() * 50,
    };

    await sandboxStore.updateInstance(sandboxId, { metrics });
    
    return metrics;
  }

  // List all sandboxes (admin function)
  async listAllSandboxes(): Promise<SandboxInstance[]> {
    return await sandboxStore.getAllInstances();
  }

  // Cleanup idle sandboxes
  private async cleanupIdleSandboxes(): Promise<void> {
    const allSandboxes = await sandboxStore.getAllInstances();
    const now = Date.now();
    
    for (const sandbox of allSandboxes) {
      const lastActivity = new Date(sandbox.lastActivityAt).getTime();
      const idleTime = now - lastActivity;
      const timeoutMs = sandbox.resources.timeoutMinutes * 60 * 1000;
      
      if (idleTime > timeoutMs && ['ready', 'running'].includes(sandbox.status)) {
        console.log(`Cleaning up idle sandbox: ${sandbox.id}`);
        await sandboxStore.updateInstance(sandbox.id, {
          status: 'timeout',
        });
        
        // In production, this would actually stop the Cloud Run instance
        await this.stopCloudRunSandbox(sandbox.id);
      }
    }
  }

  // Mock Cloud Run operations (replace with actual Cloud Run API calls)
  private async createCloudRunSandbox(sandboxId: string): Promise<void> {
    const sandbox = await sandboxStore.getInstance(sandboxId);
    if (!sandbox) return;

    // Simulate creation delay
    await new Promise(resolve => setTimeout(resolve, 2000));

    await sandboxStore.updateInstance(sandboxId, {
      status: 'starting',
      startedAt: new Date().toISOString(),
    });

    // Simulate startup delay
    await new Promise(resolve => setTimeout(resolve, 3000));

    await sandboxStore.updateInstance(sandboxId, {
      status: 'ready',
      endpointUrl: `https://sandbox-${sandboxId}-uc.a.run.app`,
    });

    console.log(`Sandbox ${sandboxId} created and ready`);
  }

  private async stopCloudRunSandbox(sandboxId: string): Promise<void> {
    // Simulate stopping delay
    await new Promise(resolve => setTimeout(resolve, 1000));
    console.log(`Sandbox ${sandboxId} stopped`);
  }

  // Calculate cost (for monitoring)
  calculateCost(sandbox: SandboxInstance): number {
    if (!sandbox.startedAt) return 0;
    
    const startTime = new Date(sandbox.startedAt).getTime();
    const endTime = sandbox.status === 'stopped' ? 
      Date.now() : 
      new Date(sandbox.lastActivityAt).getTime();
    
    const runtimeHours = (endTime - startTime) / (1000 * 60 * 60);
    
    // Simple cost calculation based on resources
    const cpuCost = sandbox.resources.cpuCores * 0.05; // $0.05 per CPU hour
    const memoryCost = (sandbox.resources.memoryMB / 1024) * 0.01; // $0.01 per GB hour
    const gpuCost = sandbox.resources.gpuType === 'nvidia-v100' ? 2.0 : 
                   sandbox.resources.gpuType === 'nvidia-t4' ? 0.5 : 0;
    
    return (cpuCost + memoryCost + gpuCost) * runtimeHours;
  }

  // Cleanup on shutdown
  destroy(): void {
    if (this.cleanupInterval) {
      clearInterval(this.cleanupInterval);
    }
  }
}

// Export singleton instance
export const sandboxOrchestrator = new SandboxOrchestrator();