import { z } from 'zod';
import { router, protectedProcedure, editorProcedure, createErrorHandler } from '../trpc';
import { TRPCError } from '@trpc/server';

// Input schemas
const createPaperSchema = z.object({
  title: z.string().min(1),
  description: z.string().optional(),
  templateId: z.enum(['ieee', 'acm', 'blank']).default('blank'),
  content: z.object({
    latex: z.string().default(''),
    bibliography: z.string().default(''),
  }),
});

const updatePaperSchema = z.object({
  id: z.string(),
  title: z.string().min(1).optional(),
  description: z.string().optional(),
  status: z.enum(['draft', 'in_progress', 'completed', 'archived']).optional(),
  content: z.object({
    latex: z.string().optional(),
    bibliography: z.string().optional(),
  }).optional(),
});

// const paperVersionSchema = z.object({
//   paperId: z.string(),
//   content: z.object({
//     latex: z.string(),
//     bibliography: z.string(),
//   }),
//   comment: z.string().optional(),
// });

// Output schemas
const paperSchema = z.object({
  id: z.string(),
  title: z.string(),
  description: z.string().optional(),
  templateId: z.string(),
  status: z.enum(['draft', 'in_progress', 'completed', 'archived']),
  content: z.object({
    latex: z.string(),
    bibliography: z.string(),
  }),
  createdAt: z.date(),
  updatedAt: z.date(),
  createdBy: z.string(),
  collaborators: z.array(z.string()),
  version: z.number(),
  wordCount: z.number(),
  citationCount: z.number(),
});

const paperVersionHistorySchema = z.object({
  id: z.string(),
  paperId: z.string(),
  version: z.number(),
  content: z.object({
    latex: z.string(),
    bibliography: z.string(),
  }),
  comment: z.string().optional(),
  createdAt: z.date(),
  createdBy: z.string(),
  wordCount: z.number(),
  changes: z.object({
    added: z.number(),
    removed: z.number(),
    modified: z.number(),
  }),
});

export const papersRouter = router({
  // Get all papers for current user
  list: protectedProcedure
    .input(z.object({
      status: z.enum(['draft', 'in_progress', 'completed', 'archived']).optional(),
      limit: z.number().min(1).max(100).default(20),
      cursor: z.string().optional(),
    }))
    .output(z.object({
      papers: z.array(paperSchema),
      nextCursor: z.string().optional(),
    }))
    .query(async ({ ctx, input }) => {
      try {
        let query = ctx.firestore
          .collection('papers')
          .where('createdBy', '==', ctx.user.uid)
          .orderBy('updatedAt', 'desc');

        if (input.status) {
          query = query.where('status', '==', input.status);
        }

        if (input.cursor) {
          const cursorDoc = await ctx.firestore
            .collection('papers')
            .doc(input.cursor)
            .get();
          if (cursorDoc.exists) {
            query = query.startAfter(cursorDoc);
          }
        }

        const snapshot = await query.limit(input.limit + 1).get();
        const papers = [];
        let nextCursor: string | undefined;

        for (let i = 0; i < snapshot.docs.length; i++) {
          if (i === input.limit) {
            const cursorDoc = snapshot.docs[i];
            if (cursorDoc) {
              nextCursor = cursorDoc.id;
            }
            break;
          }

          const doc = snapshot.docs[i];
          if (!doc) continue;
          const data = doc.data();
          
          papers.push({
            id: doc.id,
            title: data.title,
            description: data.description,
            templateId: data.templateId,
            status: data.status,
            content: data.content,
            createdAt: data.createdAt.toDate(),
            updatedAt: data.updatedAt.toDate(),
            createdBy: data.createdBy,
            collaborators: data.collaborators || [],
            version: data.version || 1,
            wordCount: data.wordCount || 0,
            citationCount: data.citationCount || 0,
          });
        }

        return { papers, nextCursor };
      } catch (error) {
        throw createErrorHandler('List papers')(error);
      }
    }),

  // Get a specific paper
  get: protectedProcedure
    .input(z.object({ id: z.string() }))
    .output(paperSchema)
    .query(async ({ ctx, input }) => {
      try {
        const doc = await ctx.firestore
          .collection('papers')
          .doc(input.id)
          .get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Paper not found',
          });
        }

        const data = doc.data()!;

        // Check if user has access
        if (data.createdBy !== ctx.user.uid && 
            !data.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        return {
          id: doc.id,
          title: data.title,
          description: data.description,
          templateId: data.templateId,
          status: data.status,
          content: data.content,
          createdAt: data.createdAt.toDate(),
          updatedAt: data.updatedAt.toDate(),
          createdBy: data.createdBy,
          collaborators: data.collaborators || [],
          version: data.version || 1,
          wordCount: data.wordCount || 0,
          citationCount: data.citationCount || 0,
        };
      } catch (error) {
        throw createErrorHandler('Get paper')(error);
      }
    }),

  // Create a new paper
  create: editorProcedure
    .input(createPaperSchema)
    .output(paperSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const now = new Date();
        
        // Generate initial LaTeX content based on template
        let initialLatex = input.content.latex;
        if (!initialLatex && input.templateId !== 'blank') {
          initialLatex = generateTemplateContent(input.templateId, input.title);
        }

        const paperData = {
          title: input.title,
          description: input.description,
          templateId: input.templateId,
          status: 'draft' as const,
          content: {
            latex: initialLatex,
            bibliography: input.content.bibliography,
          },
          createdAt: now,
          updatedAt: now,
          createdBy: ctx.user.uid,
          collaborators: [],
          version: 1,
          wordCount: countWords(initialLatex),
          citationCount: countCitations(input.content.bibliography),
        };

        const docRef = await ctx.firestore
          .collection('papers')
          .add(paperData);

        // Create initial version
        await ctx.firestore
          .collection('paper_versions')
          .add({
            paperId: docRef.id,
            version: 1,
            content: paperData.content,
            comment: 'Initial version',
            createdAt: now,
            createdBy: ctx.user.uid,
            wordCount: paperData.wordCount,
            changes: { added: paperData.wordCount, removed: 0, modified: 0 },
          });

        return {
          id: docRef.id,
          ...paperData,
        };
      } catch (error) {
        throw createErrorHandler('Create paper')(error);
      }
    }),

  // Update a paper
  update: protectedProcedure
    .input(updatePaperSchema)
    .output(paperSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const docRef = ctx.firestore.collection('papers').doc(input.id);
        const doc = await docRef.get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Paper not found',
          });
        }

        const currentData = doc.data()!;

        // Check permissions
        if (currentData.createdBy !== ctx.user.uid && 
            !currentData.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        const now = new Date();
        const updateData: Record<string, unknown> = {
          updatedAt: now,
        };

        if (input.title) updateData.title = input.title;
        if (input.description !== undefined) updateData.description = input.description;
        if (input.status) updateData.status = input.status;
        
        if (input.content) {
          const newContent = {
            latex: input.content.latex ?? currentData.content.latex,
            bibliography: input.content.bibliography ?? currentData.content.bibliography,
          };
          
          updateData.content = newContent;
          updateData.wordCount = countWords(newContent.latex);
          updateData.citationCount = countCitations(newContent.bibliography);
          
          // Create new version if content changed
          if (input.content.latex && input.content.latex !== currentData.content.latex) {
            updateData.version = (currentData.version || 1) + 1;
            
            const changes = calculateChanges(currentData.content.latex, newContent.latex);
            
            await ctx.firestore
              .collection('paper_versions')
              .add({
                paperId: input.id,
                version: updateData.version,
                content: newContent,
                createdAt: now,
                createdBy: ctx.user.uid,
                wordCount: updateData.wordCount,
                changes,
              });
          }
        }

        await docRef.update(updateData);

        const updatedDoc = await docRef.get();
        const updatedData = updatedDoc.data()!;

        return {
          id: input.id,
          title: updatedData.title,
          description: updatedData.description,
          templateId: updatedData.templateId,
          status: updatedData.status,
          content: updatedData.content,
          createdAt: updatedData.createdAt.toDate(),
          updatedAt: updatedData.updatedAt.toDate(),
          createdBy: updatedData.createdBy,
          collaborators: updatedData.collaborators || [],
          version: updatedData.version || 1,
          wordCount: updatedData.wordCount || 0,
          citationCount: updatedData.citationCount || 0,
        };
      } catch (error) {
        throw createErrorHandler('Update paper')(error);
      }
    }),

  // Delete a paper
  delete: protectedProcedure
    .input(z.object({ id: z.string() }))
    .mutation(async ({ ctx, input }) => {
      try {
        const docRef = ctx.firestore.collection('papers').doc(input.id);
        const doc = await docRef.get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Paper not found',
          });
        }

        const data = doc.data()!;

        // Only creator can delete
        if (data.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Only the creator can delete this paper',
          });
        }

        // Delete versions
        const versionsSnapshot = await ctx.firestore
          .collection('paper_versions')
          .where('paperId', '==', input.id)
          .get();

        const batch = ctx.firestore.batch();
        versionsSnapshot.docs.forEach(doc => batch.delete(doc.ref));
        batch.delete(docRef);

        await batch.commit();

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Delete paper')(error);
      }
    }),

  // Get version history
  getVersions: protectedProcedure
    .input(z.object({ paperId: z.string() }))
    .output(z.array(paperVersionHistorySchema))
    .query(async ({ ctx, input }) => {
      try {
        // Check paper access first
        const paperDoc = await ctx.firestore
          .collection('papers')
          .doc(input.paperId)
          .get();

        if (!paperDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Paper not found',
          });
        }

        const paperData = paperDoc.data()!;
        if (paperData.createdBy !== ctx.user.uid && 
            !paperData.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        const versionsSnapshot = await ctx.firestore
          .collection('paper_versions')
          .where('paperId', '==', input.paperId)
          .orderBy('version', 'desc')
          .get();

        return versionsSnapshot.docs.map(doc => {
          const data = doc.data();
          return {
            id: doc.id,
            paperId: data.paperId,
            version: data.version,
            content: data.content,
            comment: data.comment,
            createdAt: data.createdAt.toDate(),
            createdBy: data.createdBy,
            wordCount: data.wordCount,
            changes: data.changes,
          };
        });
      } catch (error) {
        throw createErrorHandler('Get versions')(error);
      }
    }),

  // Add collaborator
  addCollaborator: editorProcedure
    .input(z.object({
      paperId: z.string(),
      email: z.string().email(),
    }))
    .mutation(async ({ ctx, input }) => {
      try {
        const paperRef = ctx.firestore.collection('papers').doc(input.paperId);
        const paperDoc = await paperRef.get();

        if (!paperDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Paper not found',
          });
        }

        const paperData = paperDoc.data()!;
        if (paperData.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Only the creator can add collaborators',
          });
        }

        // Find user by email
        const userSnapshot = await ctx.firestore
          .collection('users')
          .where('email', '==', input.email)
          .limit(1)
          .get();

        if (userSnapshot.empty) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User not found',
          });
        }

        const userDoc = userSnapshot.docs[0];
        if (!userDoc) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User document not found',
          });
        }
        const userId = userDoc.id;
        const collaborators = paperData.collaborators || [];

        if (!collaborators.includes(userId)) {
          collaborators.push(userId);
          await paperRef.update({ collaborators });
        }

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Add collaborator')(error);
      }
    }),
});

// Helper functions
function generateTemplateContent(templateId: string, title: string): string {
  switch (templateId) {
    case 'ieee':
      return `\\documentclass[conference]{IEEEtran}
\\usepackage{cite}
\\usepackage{amsmath,amssymb,amsfonts}
\\usepackage{algorithmic}
\\usepackage{graphicx}
\\usepackage{textcomp}
\\usepackage{xcolor}

\\begin{document}

\\title{${title}}

\\author{\\IEEEauthorblockN{Author Name}
\\IEEEauthorblockA{\\textit{Department} \\\\
\\textit{University Name}\\\\
City, Country \\\\
email@university.edu}}

\\maketitle

\\begin{abstract}
This document is a template for your IEEE conference paper.
\\end{abstract}

\\begin{IEEEkeywords}
keyword1, keyword2, keyword3
\\end{IEEEkeywords}

\\section{Introduction}
Your introduction goes here.

\\section{Methodology}
Your methodology goes here.

\\section{Results}
Your results go here.

\\section{Conclusion}
Your conclusion goes here.

\\begin{thebibliography}{00}
\\bibitem{b1} Reference 1
\\end{thebibliography}

\\end{document}`;

    case 'acm':
      return `\\documentclass[sigconf]{acmart}

\\begin{document}

\\title{${title}}

\\author{Author Name}
\\affiliation{%
  \\institution{University Name}
  \\city{City}
  \\country{Country}
}
\\email{email@university.edu}

\\begin{abstract}
This document is a template for your ACM conference paper.
\\end{abstract}

\\keywords{keyword1, keyword2, keyword3}

\\maketitle

\\section{Introduction}
Your introduction goes here.

\\section{Related Work}
Your related work goes here.

\\section{Methodology}
Your methodology goes here.

\\section{Results}
Your results go here.

\\section{Conclusion}
Your conclusion goes here.

\\bibliographystyle{ACM-Reference-Format}
\\bibliography{references}

\\end{document}`;

    default:
      return `\\documentclass{article}
\\usepackage[utf8]{inputenc}
\\usepackage{amsmath}
\\usepackage{amsfonts}
\\usepackage{amssymb}
\\usepackage{graphicx}

\\title{${title}}
\\author{Your Name}
\\date{\\today}

\\begin{document}

\\maketitle

\\begin{abstract}
Your abstract goes here.
\\end{abstract}

\\section{Introduction}
Your introduction goes here.

\\section{Conclusion}
Your conclusion goes here.

\\bibliographystyle{plain}
\\bibliography{references}

\\end{document}`;
  }
}

function countWords(latex: string): number {
  // Simple word count - remove LaTeX commands and count words
  const text = latex
    .replace(/\\[a-zA-Z]+\{[^}]*\}/g, '') // Remove commands with arguments
    .replace(/\\[a-zA-Z]+/g, '') // Remove commands without arguments
    .replace(/\{[^}]*\}/g, '') // Remove remaining braces
    .replace(/\$[^$]*\$/g, '') // Remove inline math
    .replace(/\$\$[^$]*\$\$/g, '') // Remove display math
    .replace(/[^a-zA-Z\s]/g, ' ') // Replace non-letters with spaces
    .trim();
  
  return text ? text.split(/\s+/).length : 0;
}

function countCitations(bibliography: string): number {
  // Count @article, @book, etc. entries
  const matches = bibliography.match(/@\w+\{/g);
  return matches ? matches.length : 0;
}

function calculateChanges(oldText: string, newText: string) {
  // Simple diff calculation - in practice, you'd use a proper diff algorithm
  const oldWords = oldText.split(/\s+/).length;
  const newWords = newText.split(/\s+/).length;
  
  const added = Math.max(0, newWords - oldWords);
  const removed = Math.max(0, oldWords - newWords);
  const modified = Math.min(oldWords, newWords);
  
  return { added, removed, modified };
}