/**
 * MARIA Auth API Router
 * Handles authentication, registration, and plan management
 */

import { z } from 'zod';
import { router, publicProcedure } from '../trpc';
import { TRPCError } from '@trpc/server';
import { 
  mariaAuth, 
  CreateUserSchema, 
  LoginSchema, 
  PlanType,
  PLAN_DEFINITIONS 
} from '../lib/maria-auth';

// Session token validation middleware
const withSession = publicProcedure.use(async ({ next, ctx }) => {
  const token = ctx.req?.headers?.authorization?.replace('Bearer ', '');
  
  if (!token) {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Session token required',
    });
  }

  try {
    const session = await mariaAuth.getSession(token);
    return next({
      ctx: {
        ...ctx,
        session,
        user: {
          uid: session.uid,
          email: session.email,
          name: session.name,
          plan: session.plan,
        },
      },
    });
  } catch {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Invalid or expired session',
    });
  }
});

export const mariaAuthRouter = router({
  // User registration
  register: publicProcedure
    .input(CreateUserSchema)
    .mutation(async ({ input }) => {
      try {
        const result = await mariaAuth.register(input);
        return {
          success: true,
          data: result,
        };
      } catch (error) {
        throw new TRPCError({
          code: 'BAD_REQUEST',
          message: error instanceof Error ? error.message : 'Registration failed',
        });
      }
    }),

  // User login
  login: publicProcedure
    .input(LoginSchema)
    .mutation(async ({ input }) => {
      try {
        const result = await mariaAuth.login(input);
        return {
          success: true,
          data: result,
        };
      } catch (error) {
        throw new TRPCError({
          code: 'UNAUTHORIZED',
          message: error instanceof Error ? error.message : 'Login failed',
        });
      }
    }),

  // Get current user session
  getSession: withSession.query(async ({ ctx }) => {
    return {
      success: true,
      data: ctx.session,
    };
  }),

  // Logout
  logout: withSession.mutation(async ({ ctx }) => {
    const token = ctx.req?.headers?.authorization?.replace('Bearer ', '');
    if (!token) {
      throw new TRPCError({
        code: 'BAD_REQUEST',
        message: 'No session token provided',
      });
    }

    try {
      const result = await mariaAuth.logout(token);
      return {
        success: true,
        data: result,
      };
    } catch {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: 'Logout failed',
      });
    }
  }),

  // Get user profile
  getProfile: withSession.query(async ({ ctx }) => {
    return {
      success: true,
      data: {
        uid: ctx.session.uid,
        email: ctx.session.email,
        name: ctx.session.name,
        plan: ctx.session.plan,
        usage: ctx.session.usage,
        createdAt: ctx.session.createdAt,
        lastLoginAt: ctx.session.lastLoginAt,
        isEmailVerified: ctx.session.isEmailVerified,
        mfaEnabled: ctx.session.mfaEnabled,
      },
    };
  }),

  // Get plan information
  getPlanInfo: publicProcedure
    .input(z.object({ planType: PlanType }))
    .query(async ({ input }) => {
      const planInfo = mariaAuth.getPlanInfo(input.planType);
      return {
        success: true,
        data: planInfo,
      };
    }),

  // Get all plans
  getAllPlans: publicProcedure.query(async () => {
    return {
      success: true,
      data: PLAN_DEFINITIONS,
    };
  }),

  // Check usage limits
  checkLimits: withSession.query(async ({ ctx }) => {
    try {
      const dailyOk = await mariaAuth.checkLimits(ctx.session.uid, 'dailyRequests');
      const monthlyOk = await mariaAuth.checkLimits(ctx.session.uid, 'monthlyRequests');
      
      return {
        success: true,
        data: {
          canMakeRequest: dailyOk && monthlyOk,
          limits: {
            daily: {
              current: ctx.session.usage.dailyRequests,
              limit: ctx.session.plan.limits.dailyRequests,
              available: dailyOk,
            },
            monthly: {
              current: ctx.session.usage.monthlyRequests,
              limit: ctx.session.plan.limits.monthlyRequests,
              available: monthlyOk,
            },
          },
        },
      };
    } catch {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: 'Failed to check limits',
      });
    }
  }),

  // Track API usage
  trackUsage: withSession.mutation(async ({ ctx }) => {
    try {
      const usage = await mariaAuth.trackUsage(ctx.session.uid);
      return {
        success: true,
        data: usage,
      };
    } catch {
      throw new TRPCError({
        code: 'INTERNAL_SERVER_ERROR',
        message: 'Failed to track usage',
      });
    }
  }),

  // Development endpoints (remove in production)
  dev: router({
    // Get all users
    getAllUsers: publicProcedure.query(async () => {
      return {
        success: true,
        data: mariaAuth.getAllUsers(),
      };
    }),

    // Create test users
    createTestUsers: publicProcedure.mutation(async () => {
      const testUsers = [
        {
          email: 'free@maria-platform.dev',
          password: 'testpass123',
          name: 'Free User',
          plan: 'free' as const,
        },
        {
          email: 'pro@maria-platform.dev',
          password: 'testpass123',
          name: 'Pro User',
          plan: 'pro' as const,
        },
        {
          email: 'max@maria-platform.dev',
          password: 'testpass123',
          name: 'Max User',
          plan: 'max' as const,
        },
      ];

      const results = [];
      for (const userData of testUsers) {
        try {
          const result = await mariaAuth.register(userData);
          results.push({
            email: userData.email,
            plan: userData.plan,
            status: 'created',
            sessionToken: result.sessionToken,
          });
        } catch (error) {
          results.push({
            email: userData.email,
            plan: userData.plan,
            status: 'error',
            error: error instanceof Error ? error.message : 'Unknown error',
          });
        }
      }

      return {
        success: true,
        data: results,
      };
    }),
  }),
});