import { z } from 'zod';
import { router, protectedProcedure, createErrorHandler } from '../trpc';
import { TRPCError } from '@trpc/server';

// Input schemas

const updateProfileSchema = z.object({
  name: z.string().min(1).optional(),
  bio: z.string().optional(),
  preferences: z.object({
    theme: z.enum(['light', 'dark', 'system']).optional(),
    language: z.string().optional(),
    fontSize: z.enum(['small', 'medium', 'large']).optional(),
    autoSave: z.boolean().optional(),
    autoSaveInterval: z.number().min(10).max(300).optional(),
    notifications: z.object({
      email: z.boolean().optional(),
      push: z.boolean().optional(),
      mentions: z.boolean().optional(),
      updates: z.boolean().optional(),
    }).optional(),
  }).optional(),
  privacy: z.object({
    profileVisibility: z.enum(['public', 'private', 'team']).optional(),
    activityStatus: z.boolean().optional(),
    showEmail: z.boolean().optional(),
  }).optional(),
  integrations: z.object({
    googleSlides: z.object({
      enabled: z.boolean().optional(),
      autoSync: z.boolean().optional(),
    }).optional(),
    github: z.object({
      enabled: z.boolean().optional(),
      username: z.string().optional(),
    }).optional(),
    neo4j: z.object({
      enabled: z.boolean().optional(),
      instanceId: z.string().optional(),
    }).optional(),
  }).optional(),
});

// Output schemas
const userSchema = z.object({
  uid: z.string(),
  email: z.string().email(),
  name: z.string(),
  role: z.enum(['admin', 'editor', 'viewer']),
  emailVerified: z.boolean(),
  createdAt: z.date(),
  updatedAt: z.date(),
  bio: z.string().optional(),
  avatar: z.string().optional(),
  preferences: z.object({
    theme: z.enum(['light', 'dark', 'system']),
    language: z.string(),
    fontSize: z.enum(['small', 'medium', 'large']),
    autoSave: z.boolean(),
    autoSaveInterval: z.number(),
    notifications: z.object({
      email: z.boolean(),
      push: z.boolean(),
      mentions: z.boolean(),
      updates: z.boolean(),
    }),
  }),
  privacy: z.object({
    profileVisibility: z.enum(['public', 'private', 'team']),
    activityStatus: z.boolean(),
    showEmail: z.boolean(),
  }),
  integrations: z.object({
    googleSlides: z.object({
      enabled: z.boolean(),
      autoSync: z.boolean(),
    }),
    github: z.object({
      enabled: z.boolean(),
      username: z.string(),
    }),
    neo4j: z.object({
      enabled: z.boolean(),
      instanceId: z.string(),
    }),
  }),
});

export const authRouter = router({
  // Get current user profile
  me: protectedProcedure
    .output(userSchema)
    .query(async ({ ctx }) => {
      try {
        const userDoc = await ctx.firestore
          .collection('users')
          .doc(ctx.user.uid)
          .get();

        if (!userDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User profile not found',
          });
        }

        const userData = userDoc.data()!;
        
        return {
          uid: ctx.user.uid,
          email: ctx.user.email!,
          name: userData.name,
          role: ctx.user.role,
          emailVerified: ctx.user.emailVerified,
          createdAt: userData.createdAt?.toDate() || new Date(),
          updatedAt: userData.updatedAt?.toDate() || new Date(),
          bio: userData.bio,
          avatar: userData.avatar,
          preferences: {
            theme: userData.preferences?.theme || 'system',
            language: userData.preferences?.language || 'en',
            fontSize: userData.preferences?.fontSize || 'medium',
            autoSave: userData.preferences?.autoSave ?? true,
            autoSaveInterval: userData.preferences?.autoSaveInterval || 30,
            notifications: {
              email: userData.preferences?.notifications?.email ?? true,
              push: userData.preferences?.notifications?.push ?? true,
              mentions: userData.preferences?.notifications?.mentions ?? true,
              updates: userData.preferences?.notifications?.updates ?? false,
            },
          },
          privacy: {
            profileVisibility: userData.privacy?.profileVisibility || 'team',
            activityStatus: userData.privacy?.activityStatus ?? true,
            showEmail: userData.privacy?.showEmail ?? false,
          },
          integrations: {
            googleSlides: {
              enabled: userData.integrations?.googleSlides?.enabled ?? false,
              autoSync: userData.integrations?.googleSlides?.autoSync ?? false,
            },
            github: {
              enabled: userData.integrations?.github?.enabled ?? false,
              username: userData.integrations?.github?.username || '',
            },
            neo4j: {
              enabled: userData.integrations?.neo4j?.enabled ?? true,
              instanceId: userData.integrations?.neo4j?.instanceId || '4234c1a0',
            },
          },
        };
      } catch (error) {
        throw createErrorHandler('Get user profile')(error);
      }
    }),

  // Update user profile
  updateProfile: protectedProcedure
    .input(updateProfileSchema)
    .output(userSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const userRef = ctx.firestore.collection('users').doc(ctx.user.uid);
        
        // Build update object
        const updateData: Record<string, unknown> = {
          updatedAt: new Date(),
        };

        if (input.name) updateData.name = input.name;
        if (input.bio !== undefined) updateData.bio = input.bio;
        if (input.preferences) {
          updateData.preferences = input.preferences;
        }
        if (input.privacy) {
          updateData.privacy = input.privacy;
        }
        if (input.integrations) {
          updateData.integrations = input.integrations;
        }

        await userRef.update(updateData);

        // Return updated user data
        const updatedDoc = await userRef.get();
        const userData = updatedDoc.data()!;

        return {
          uid: ctx.user.uid,
          email: ctx.user.email!,
          name: userData.name,
          role: ctx.user.role,
          emailVerified: ctx.user.emailVerified,
          createdAt: userData.createdAt?.toDate() || new Date(),
          updatedAt: userData.updatedAt?.toDate() || new Date(),
          bio: userData.bio,
          avatar: userData.avatar,
          preferences: {
            theme: userData.preferences?.theme || 'system',
            language: userData.preferences?.language || 'en',
            fontSize: userData.preferences?.fontSize || 'medium',
            autoSave: userData.preferences?.autoSave ?? true,
            autoSaveInterval: userData.preferences?.autoSaveInterval || 30,
            notifications: {
              email: userData.preferences?.notifications?.email ?? true,
              push: userData.preferences?.notifications?.push ?? true,
              mentions: userData.preferences?.notifications?.mentions ?? true,
              updates: userData.preferences?.notifications?.updates ?? false,
            },
          },
          privacy: {
            profileVisibility: userData.privacy?.profileVisibility || 'team',
            activityStatus: userData.privacy?.activityStatus ?? true,
            showEmail: userData.privacy?.showEmail ?? false,
          },
          integrations: {
            googleSlides: {
              enabled: userData.integrations?.googleSlides?.enabled ?? false,
              autoSync: userData.integrations?.googleSlides?.autoSync ?? false,
            },
            github: {
              enabled: userData.integrations?.github?.enabled ?? false,
              username: userData.integrations?.github?.username || '',
            },
            neo4j: {
              enabled: userData.integrations?.neo4j?.enabled ?? true,
              instanceId: userData.integrations?.neo4j?.instanceId || '4234c1a0',
            },
          },
        };
      } catch (error) {
        throw createErrorHandler('Update profile')(error);
      }
    }),

  // Get user activity status
  getActivityStatus: protectedProcedure
    .output(z.object({
      isOnline: z.boolean(),
      lastSeen: z.date(),
      currentProject: z.string().optional(),
    }))
    .query(async ({ ctx }) => {
      try {
        const activityDoc = await ctx.firestore
          .collection('user_activity')
          .doc(ctx.user.uid)
          .get();

        if (!activityDoc.exists) {
          return {
            isOnline: false,
            lastSeen: new Date(),
            currentProject: undefined,
          };
        }

        const activityData = activityDoc.data()!;
        const lastSeen = activityData.lastSeen?.toDate() || new Date();
        const isOnline = Date.now() - lastSeen.getTime() < 5 * 60 * 1000; // 5 minutes

        return {
          isOnline,
          lastSeen,
          currentProject: activityData.currentProject,
        };
      } catch (error) {
        throw createErrorHandler('Get activity status')(error);
      }
    }),

  // Update activity status
  updateActivity: protectedProcedure
    .input(z.object({
      currentProject: z.string().optional(),
    }))
    .mutation(async ({ ctx, input }) => {
      try {
        await ctx.firestore
          .collection('user_activity')
          .doc(ctx.user.uid)
          .set({
            lastSeen: new Date(),
            currentProject: input.currentProject,
          }, { merge: true });

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Update activity')(error);
      }
    }),

  // List team members (for collaboration features)
  getTeamMembers: protectedProcedure
    .output(z.array(z.object({
      uid: z.string(),
      name: z.string(),
      email: z.string().optional(),
      role: z.enum(['admin', 'editor', 'viewer']),
      isOnline: z.boolean(),
      lastSeen: z.date(),
    })))
    .query(async ({ ctx }) => {
      try {
        // For now, return users from the same organization/team
        // In a real implementation, you'd have team/organization logic
        const usersSnapshot = await ctx.firestore
          .collection('users')
          .where('privacy.profileVisibility', 'in', ['public', 'team'])
          .limit(50)
          .get();

        const teamMembers = await Promise.all(
          usersSnapshot.docs.map(async (doc) => {
            const userData = doc.data();
            
            // Get activity status
            const activityDoc = await ctx.firestore
              .collection('user_activity')
              .doc(doc.id)
              .get();
            
            const activityData = activityDoc.data();
            const lastSeen = activityData?.lastSeen?.toDate() || new Date(0);
            const isOnline = Date.now() - lastSeen.getTime() < 5 * 60 * 1000;

            return {
              uid: doc.id,
              name: userData.name,
              email: userData.privacy?.showEmail ? userData.email : undefined,
              role: userData.role,
              isOnline,
              lastSeen,
            };
          })
        );

        return teamMembers;
      } catch (error) {
        throw createErrorHandler('Get team members')(error);
      }
    }),

  // Alias for compatibility with tests
  getProfile: protectedProcedure
    .output(userSchema)
    .query(async ({ ctx }) => {
      try {
        const userDoc = await ctx.firestore
          .collection('users')
          .doc(ctx.user.uid)
          .get();

        if (!userDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User profile not found',
          });
        }

        const userData = userDoc.data()!;
        
        return {
          uid: ctx.user.uid,
          email: ctx.user.email!,
          name: userData.name,
          role: ctx.user.role,
          emailVerified: ctx.user.emailVerified,
          createdAt: userData.createdAt?.toDate() || new Date(),
          updatedAt: userData.updatedAt?.toDate() || new Date(),
          bio: userData.bio,
          avatar: userData.avatar,
          preferences: {
            theme: userData.preferences?.theme || 'system',
            language: userData.preferences?.language || 'en',
            fontSize: userData.preferences?.fontSize || 'medium',
            autoSave: userData.preferences?.autoSave ?? true,
            autoSaveInterval: userData.preferences?.autoSaveInterval || 30,
            notifications: {
              email: userData.preferences?.notifications?.email ?? true,
              push: userData.preferences?.notifications?.push ?? true,
              mentions: userData.preferences?.notifications?.mentions ?? true,
              updates: userData.preferences?.notifications?.updates ?? false,
            },
          },
          privacy: {
            profileVisibility: userData.privacy?.profileVisibility || 'team',
            activityStatus: userData.privacy?.activityStatus ?? true,
            showEmail: userData.privacy?.showEmail ?? false,
          },
          integrations: {
            googleSlides: {
              enabled: userData.integrations?.googleSlides?.enabled ?? false,
              autoSync: userData.integrations?.googleSlides?.autoSync ?? false,
            },
            github: {
              enabled: userData.integrations?.github?.enabled ?? false,
              username: userData.integrations?.github?.username || '',
            },
            neo4j: {
              enabled: userData.integrations?.neo4j?.enabled ?? true,
              instanceId: userData.integrations?.neo4j?.instanceId || '4234c1a0',
            },
          },
        };
      } catch (error) {
        throw createErrorHandler('Get user profile')(error);
      }
    }),

  // Get user settings (alias for profile preferences)
  getSettings: protectedProcedure
    .output(z.object({
      theme: z.enum(['light', 'dark', 'system']),
      language: z.string(),
      fontSize: z.enum(['small', 'medium', 'large']),
      autoSave: z.boolean(),
      autoSaveInterval: z.number(),
      notifications: z.object({
        email: z.boolean(),
        push: z.boolean(),
        mentions: z.boolean(),
        updates: z.boolean(),
      }),
    }))
    .query(async ({ ctx }) => {
      try {
        const userDoc = await ctx.firestore
          .collection('users')
          .doc(ctx.user.uid)
          .get();

        if (!userDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User settings not found',
          });
        }

        const userData = userDoc.data()!;
        
        return {
          theme: userData.preferences?.theme || 'system',
          language: userData.preferences?.language || 'en',
          fontSize: userData.preferences?.fontSize || 'medium',
          autoSave: userData.preferences?.autoSave ?? true,
          autoSaveInterval: userData.preferences?.autoSaveInterval || 30,
          notifications: {
            email: userData.preferences?.notifications?.email ?? true,
            push: userData.preferences?.notifications?.push ?? true,
            mentions: userData.preferences?.notifications?.mentions ?? true,
            updates: userData.preferences?.notifications?.updates ?? false,
          },
        };
      } catch (error) {
        throw createErrorHandler('Get user settings')(error);
      }
    }),
});