import { initTRPC, TRPCError } from '@trpc/server';
import type { CreateExpressContextOptions } from '@trpc/server/adapters/express';
import superjson from 'superjson';
import { ZodError } from 'zod';
import { getAuth } from 'firebase-admin/auth';
import { Firestore } from '@google-cloud/firestore';

// Initialize Firestore
const firestore = new Firestore({
  projectId: process.env.MARIA_PROJECT_ID || 'maria-code',
});

// Context interface
export interface Context {
  req: CreateExpressContextOptions['req'];
  res: CreateExpressContextOptions['res'];
  user?: {
    uid: string;
    email?: string;
    role: 'admin' | 'editor' | 'viewer';
    emailVerified: boolean;
  };
  firestore: Firestore;
}

// Protected context interface - user is guaranteed to be defined
export interface ProtectedContext extends Context {
  user: {
    uid: string;
    email?: string;
    role: 'admin' | 'editor' | 'viewer';
    emailVerified: boolean;
  };
}

// Create context
export const createContext = async ({
  req,
  res,
}: CreateExpressContextOptions): Promise<Context> => {
  // Extract JWT token from Authorization header
  const authHeader = req.headers.authorization;
  let user: Context['user'] | undefined;

  if (authHeader && authHeader.startsWith('Bearer ')) {
    const token = authHeader.substring(7);
    
    try {
      // Verify Firebase ID token
      const decodedToken = await getAuth().verifyIdToken(token);
      
      // Get user role from Firestore
      const userDoc = await firestore
        .collection('users')
        .doc(decodedToken.uid)
        .get();
      
      const userData = userDoc.data();
      
      user = {
        uid: decodedToken.uid,
        email: decodedToken.email,
        role: userData?.role || 'viewer',
        emailVerified: decodedToken.email_verified || false,
      };
    } catch {
      // Don't throw error here, let protected procedures handle it
    }
  }

  return {
    req,
    res,
    user,
    firestore,
  };
};

// Initialize tRPC
const t = initTRPC.context<Context>().create({
  transformer: superjson,
  errorFormatter({ shape, error }) {
    return {
      ...shape,
      data: {
        ...shape.data,
        zodError:
          error.cause instanceof ZodError ? error.cause.flatten() : null,
      },
    };
  },
});


// Base router and procedure
export const router = t.router;
export const procedure = t.procedure;

// Public procedure (alias for compatibility)
export const publicProcedure = procedure;

// Authentication middleware with proper type narrowing
const isAuthenticated = t.middleware(({ ctx, next }) => {
  if (!ctx.user) {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Authentication required',
    });
  }
  
  // Type guard to ensure user is defined
  const authenticatedCtx: ProtectedContext = {
    ...ctx,
    user: ctx.user as NonNullable<typeof ctx.user>,
  };
  
  return next({
    ctx: authenticatedCtx,
  });
});

// Role-based middleware - should be used after authentication
const hasRole = (requiredRole: 'admin' | 'editor' | 'viewer') =>
  t.middleware(({ ctx, next }) => {
    // This middleware assumes authentication has already been checked
    const user = (ctx as any).user;
    if (!user) {
      throw new TRPCError({
        code: 'UNAUTHORIZED',
        message: 'Authentication required',
      });
    }

    const roleHierarchy: Record<string, number> = {
      viewer: 1,
      editor: 2,
      admin: 3,
    };

    const userRole = user.role;
    const userRoleLevel = userRole ? roleHierarchy[userRole] : 0;
    const requiredRoleLevel = roleHierarchy[requiredRole] || 999;
    
    if (!userRole || !userRoleLevel || userRoleLevel < requiredRoleLevel) {
      throw new TRPCError({
        code: 'FORBIDDEN',
        message: `${requiredRole} role required`,
      });
    }

    return next({
      ctx: ctx as ProtectedContext,
    });
  });

// Email verification middleware - should be used after authentication
const isEmailVerified = t.middleware(({ ctx, next }) => {
  // This middleware assumes authentication has already been checked
  const user = (ctx as any).user;
  if (!user) {
    throw new TRPCError({
      code: 'UNAUTHORIZED',
      message: 'Authentication required',
    });
  }

  if (!user.emailVerified) {
    throw new TRPCError({
      code: 'FORBIDDEN',
      message: 'Email verification required',
    });
  }

  return next({
    ctx: ctx as ProtectedContext,
  });
});

// Protected procedures are created below

// Create protected procedures with proper authentication checks
const authenticatedProcedure = procedure.use(isAuthenticated);

export const protectedProcedure = authenticatedProcedure;
export const adminProcedure = authenticatedProcedure.use(hasRole('admin'));
export const editorProcedure = authenticatedProcedure.use(hasRole('editor'));
export const verifiedProcedure = authenticatedProcedure.use(isEmailVerified);

// Update private procedure alias after protectedProcedure is defined
export { protectedProcedure as privateProcedure };

// Create caller factory (required by test)
export const createCallerFactory = t.createCallerFactory;

// Utility functions for common operations
export const createErrorHandler = (operation: string) => (error: unknown) => {
  
  if (error instanceof TRPCError) {
    throw error;
  }
  
  if (error instanceof Error) {
    throw new TRPCError({
      code: 'INTERNAL_SERVER_ERROR',
      message: `${operation} failed: ${error.message}`,
    });
  }
  
  throw new TRPCError({
    code: 'INTERNAL_SERVER_ERROR',
    message: `${operation} failed with unknown error`,
  });
};

// Pagination helper
export const createPaginationInput = () => ({
  limit: 50,
  cursor: undefined as string | undefined,
});

// Rate limiting helper (basic implementation)
const requestCounts = new Map<string, { count: number; resetTime: number }>();

export const rateLimit = (
  requests: number = 100, 
  windowMs: number = 15 * 60 * 1000 // 15 minutes
) =>
  t.middleware(({ ctx, next }) => {
    const key = ctx.user?.uid || ctx.req.ip || 'anonymous';
    const now = Date.now();
    const windowStart = now - windowMs;
    
    const current = requestCounts.get(key);
    
    if (!current || current.resetTime < windowStart) {
      requestCounts.set(key, { count: 1, resetTime: now + windowMs });
      return next();
    }
    
    if (current.count >= requests) {
      throw new TRPCError({
        code: 'TOO_MANY_REQUESTS',
        message: 'Rate limit exceeded',
      });
    }
    
    current.count++;
    return next();
  });

// Logging middleware
export const withLogging = t.middleware(async ({ next }) => {
  const result = await next();
  
  return result;
});

// Development middleware (only for development)
export const devProcedure = process.env.NODE_ENV === 'development' 
  ? procedure.use(withLogging)
  : procedure;