import { z } from 'zod';
import { router, protectedProcedure, editorProcedure, createErrorHandler } from '../trpc';
import { TRPCError } from '@trpc/server';
import * as admin from 'firebase-admin';

// Input schemas
const createProjectSchema = z.object({
  name: z.string().min(1),
  description: z.string().optional(),
  type: z.enum(['paper', 'presentation', 'mixed']).default('mixed'),
  visibility: z.enum(['private', 'team', 'public']).default('private'),
});

const updateProjectSchema = z.object({
  id: z.string(),
  name: z.string().min(1).optional(),
  description: z.string().optional(),
  type: z.enum(['paper', 'presentation', 'mixed']).optional(),
  visibility: z.enum(['private', 'team', 'public']).optional(),
  status: z.enum(['active', 'completed', 'archived']).optional(),
});

const projectMemberSchema = z.object({
  projectId: z.string(),
  email: z.string().email(),
  role: z.enum(['viewer', 'editor', 'admin']).default('viewer'),
});

// Output schemas
const projectSchema = z.object({
  id: z.string(),
  name: z.string(),
  description: z.string().optional(),
  type: z.enum(['paper', 'presentation', 'mixed']),
  visibility: z.enum(['private', 'team', 'public']),
  status: z.enum(['active', 'completed', 'archived']),
  createdAt: z.date(),
  updatedAt: z.date(),
  createdBy: z.string(),
  members: z.array(z.object({
    uid: z.string(),
    email: z.string(),
    name: z.string(),
    role: z.enum(['viewer', 'editor', 'admin']),
    joinedAt: z.date(),
  })),
  stats: z.object({
    paperCount: z.number(),
    presentationCount: z.number(),
    totalFiles: z.number(),
    lastActivity: z.date(),
  }),
});

const projectActivitySchema = z.object({
  id: z.string(),
  projectId: z.string(),
  type: z.enum(['create', 'update', 'delete', 'share', 'comment']),
  action: z.string(),
  description: z.string(),
  createdAt: z.date(),
  createdBy: z.string(),
  createdByName: z.string(),
  metadata: z.record(z.any()).optional(),
});

export const projectsRouter = router({
  // Get all projects for current user
  list: protectedProcedure
    .input(z.object({
      type: z.enum(['paper', 'presentation', 'mixed']).optional(),
      status: z.enum(['active', 'completed', 'archived']).optional(),
      limit: z.number().min(1).max(100).default(20),
      cursor: z.string().optional(),
    }))
    .output(z.object({
      projects: z.array(projectSchema),
      nextCursor: z.string().optional(),
    }))
    .query(async ({ ctx, input }) => {
      try {
        // Get projects where user is member or creator
        let query = ctx.firestore
          .collection('projects')
          .where('memberIds', 'array-contains', ctx.user.uid)
          .orderBy('updatedAt', 'desc');

        if (input.type) {
          query = query.where('type', '==', input.type);
        }

        if (input.status) {
          query = query.where('status', '==', input.status);
        }

        if (input.cursor) {
          const cursorDoc = await ctx.firestore
            .collection('projects')
            .doc(input.cursor)
            .get();
          if (cursorDoc.exists) {
            query = query.startAfter(cursorDoc);
          }
        }

        const snapshot = await query.limit(input.limit + 1).get();
        const projects = [];
        let nextCursor: string | undefined;

        for (let i = 0; i < snapshot.docs.length; i++) {
          if (i === input.limit) {
            const cursorDoc = snapshot.docs[i];
            if (cursorDoc) {
              nextCursor = cursorDoc.id;
            }
            break;
          }

          const doc = snapshot.docs[i];
          if (!doc) continue;
          const data = doc.data();
          
          // Get project stats
          const stats = await getProjectStats(ctx, doc.id);
          
          // Get member details
          const members = await getProjectMembers(ctx, data.members || []);

          projects.push({
            id: doc.id,
            name: data.name,
            description: data.description,
            type: data.type,
            visibility: data.visibility,
            status: data.status,
            createdAt: data.createdAt.toDate(),
            updatedAt: data.updatedAt.toDate(),
            createdBy: data.createdBy,
            members,
            stats,
          });
        }

        return { projects, nextCursor };
      } catch (error) {
        throw createErrorHandler('List projects')(error);
      }
    }),

  // Get a specific project
  get: protectedProcedure
    .input(z.object({ id: z.string() }))
    .output(projectSchema)
    .query(async ({ ctx, input }) => {
      try {
        const doc = await ctx.firestore
          .collection('projects')
          .doc(input.id)
          .get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Project not found',
          });
        }

        const data = doc.data()!;

        // Check if user has access
        if (!data.memberIds?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        const stats = await getProjectStats(ctx, input.id);
        const members = await getProjectMembers(ctx, data.members || []);

        return {
          id: doc.id,
          name: data.name,
          description: data.description,
          type: data.type,
          visibility: data.visibility,
          status: data.status,
          createdAt: data.createdAt.toDate(),
          updatedAt: data.updatedAt.toDate(),
          createdBy: data.createdBy,
          members,
          stats,
        };
      } catch (error) {
        throw createErrorHandler('Get project')(error);
      }
    }),

  // Create a new project
  create: editorProcedure
    .input(createProjectSchema)
    .output(projectSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const now = new Date();
        
        const projectData = {
          name: input.name,
          description: input.description,
          type: input.type,
          visibility: input.visibility,
          status: 'active' as const,
          createdAt: now,
          updatedAt: now,
          createdBy: ctx.user.uid,
          members: [{
            uid: ctx.user.uid,
            role: 'admin' as const,
            joinedAt: now,
          }],
          memberIds: [ctx.user.uid], // For efficient querying
        };

        const docRef = await ctx.firestore
          .collection('projects')
          .add(projectData);

        // Log activity
        await logActivity(ctx, docRef.id, 'create', 'Created project', `Project "${input.name}" was created`);

        const stats = {
          paperCount: 0,
          presentationCount: 0,
          totalFiles: 0,
          lastActivity: now,
        };

        const members = await getProjectMembers(ctx, projectData.members);

        return {
          id: docRef.id,
          ...projectData,
          members,
          stats,
        };
      } catch (error) {
        throw createErrorHandler('Create project')(error);
      }
    }),

  // Update a project
  update: protectedProcedure
    .input(updateProjectSchema)
    .output(projectSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const docRef = ctx.firestore.collection('projects').doc(input.id);
        const doc = await docRef.get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Project not found',
          });
        }

        const currentData = doc.data()!;

        // Check if user is admin
        const userMember = currentData.members?.find((m: { uid: string }) => m.uid === ctx.user.uid);
        if (!userMember || userMember.role !== 'admin') {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Admin role required',
          });
        }

        const updateData: Record<string, unknown> = {
          updatedAt: new Date(),
        };

        if (input.name) updateData.name = input.name;
        if (input.description !== undefined) updateData.description = input.description;
        if (input.type) updateData.type = input.type;
        if (input.visibility) updateData.visibility = input.visibility;
        if (input.status) updateData.status = input.status;

        await docRef.update(updateData);

        // Log activity
        const changes = Object.keys(updateData).filter(k => k !== 'updatedAt');
        await logActivity(ctx, input.id, 'update', 'Updated project', `Updated: ${changes.join(', ')}`);

        const updatedDoc = await docRef.get();
        const updatedData = updatedDoc.data()!;

        const stats = await getProjectStats(ctx, input.id);
        const members = await getProjectMembers(ctx, updatedData.members || []);

        return {
          id: input.id,
          name: updatedData.name,
          description: updatedData.description,
          type: updatedData.type,
          visibility: updatedData.visibility,
          status: updatedData.status,
          createdAt: updatedData.createdAt.toDate(),
          updatedAt: updatedData.updatedAt.toDate(),
          createdBy: updatedData.createdBy,
          members,
          stats,
        };
      } catch (error) {
        throw createErrorHandler('Update project')(error);
      }
    }),

  // Delete a project
  delete: protectedProcedure
    .input(z.object({ id: z.string() }))
    .mutation(async ({ ctx, input }) => {
      try {
        const docRef = ctx.firestore.collection('projects').doc(input.id);
        const doc = await docRef.get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Project not found',
          });
        }

        const data = doc.data()!;

        // Only creator can delete
        if (data.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Only the creator can delete this project',
          });
        }

        // Delete all project activities
        const activitiesSnapshot = await ctx.firestore
          .collection('project_activities')
          .where('projectId', '==', input.id)
          .get();

        const batch = ctx.firestore.batch();
        activitiesSnapshot.docs.forEach(doc => batch.delete(doc.ref));
        batch.delete(docRef);

        await batch.commit();

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Delete project')(error);
      }
    }),

  // Add member to project
  addMember: protectedProcedure
    .input(projectMemberSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const projectRef = ctx.firestore.collection('projects').doc(input.projectId);
        const projectDoc = await projectRef.get();

        if (!projectDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Project not found',
          });
        }

        const projectData = projectDoc.data()!;
        
        // Check if user is admin
        const userMember = projectData.members?.find((m: { uid: string }) => m.uid === ctx.user.uid);
        if (!userMember || userMember.role !== 'admin') {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Admin role required',
          });
        }

        // Find user by email
        const userSnapshot = await ctx.firestore
          .collection('users')
          .where('email', '==', input.email)
          .limit(1)
          .get();

        if (userSnapshot.empty) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User not found',
          });
        }

        const userDoc = userSnapshot.docs[0];
        if (!userDoc) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User document not found',
          });
        }
        const userId = userDoc.id;
        const members = projectData.members || [];
        const memberIds = projectData.memberIds || [];

        // Check if already a member
        if (members.some((m: { uid: string }) => m.uid === userId)) {
          throw new TRPCError({
            code: 'BAD_REQUEST',
            message: 'User is already a member',
          });
        }

        const newMember = {
          uid: userId,
          role: input.role,
          joinedAt: new Date(),
        };

        members.push(newMember);
        memberIds.push(userId);

        await projectRef.update({ 
          members, 
          memberIds,
          updatedAt: new Date(),
        });

        // Log activity
        const userDocForLog = userSnapshot.docs[0];
        if (!userDocForLog) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User document not found',
          });
        }
        const userName = userDocForLog.data().name;
        await logActivity(ctx, input.projectId, 'share', 'Added member', 
          `${userName} was added as ${input.role}`);

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Add member')(error);
      }
    }),

  // Remove member from project
  removeMember: protectedProcedure
    .input(z.object({
      projectId: z.string(),
      userId: z.string(),
    }))
    .mutation(async ({ ctx, input }) => {
      try {
        const projectRef = ctx.firestore.collection('projects').doc(input.projectId);
        const projectDoc = await projectRef.get();

        if (!projectDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Project not found',
          });
        }

        const projectData = projectDoc.data()!;
        
        // Check if user is admin or removing themselves
        const userMember = projectData.members?.find((m: { uid: string }) => m.uid === ctx.user.uid);
        if (!userMember || (userMember.role !== 'admin' && ctx.user.uid !== input.userId)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Permission denied',
          });
        }

        // Can't remove the creator
        if (projectData.createdBy === input.userId) {
          throw new TRPCError({
            code: 'BAD_REQUEST',
            message: 'Cannot remove project creator',
          });
        }

        const members = (projectData.members || []).filter((m: { uid: string }) => m.uid !== input.userId);
        const memberIds = (projectData.memberIds || []).filter((id: string) => id !== input.userId);

        await projectRef.update({ 
          members, 
          memberIds,
          updatedAt: new Date(),
        });

        // Log activity
        const userDoc = await ctx.firestore.collection('users').doc(input.userId).get();
        const userName = userDoc.data()?.name || 'Unknown User';
        await logActivity(ctx, input.projectId, 'update', 'Removed member', 
          `${userName} was removed from the project`);

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Remove member')(error);
      }
    }),

  // Get project activity feed
  getActivity: protectedProcedure
    .input(z.object({
      projectId: z.string(),
      limit: z.number().min(1).max(100).default(20),
      cursor: z.string().optional(),
    }))
    .output(z.object({
      activities: z.array(projectActivitySchema),
      nextCursor: z.string().optional(),
    }))
    .query(async ({ ctx, input }) => {
      try {
        // Check project access
        const projectDoc = await ctx.firestore
          .collection('projects')
          .doc(input.projectId)
          .get();

        if (!projectDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Project not found',
          });
        }

        const projectData = projectDoc.data()!;
        if (!projectData.memberIds?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        let query = ctx.firestore
          .collection('project_activities')
          .where('projectId', '==', input.projectId)
          .orderBy('createdAt', 'desc');

        if (input.cursor) {
          const cursorDoc = await ctx.firestore
            .collection('project_activities')
            .doc(input.cursor)
            .get();
          if (cursorDoc.exists) {
            query = query.startAfter(cursorDoc);
          }
        }

        const snapshot = await query.limit(input.limit + 1).get();
        const activities = [];
        let nextCursor: string | undefined;

        for (let i = 0; i < snapshot.docs.length; i++) {
          if (i === input.limit) {
            const cursorDoc = snapshot.docs[i];
            if (cursorDoc) {
              nextCursor = cursorDoc.id;
            }
            break;
          }

          const doc = snapshot.docs[i];
          if (!doc) continue;
          const data = doc.data();
          
          activities.push({
            id: doc.id,
            projectId: data.projectId,
            type: data.type,
            action: data.action,
            description: data.description,
            createdAt: data.createdAt.toDate(),
            createdBy: data.createdBy,
            createdByName: data.createdByName,
            metadata: data.metadata,
          });
        }

        return { activities, nextCursor };
      } catch (error) {
        throw createErrorHandler('Get project activity')(error);
      }
    }),
});

// Helper functions
async function getProjectStats(ctx: { firestore: admin.firestore.Firestore }, projectId: string) {
  try {
    // Count papers
    const papersSnapshot = await ctx.firestore
      .collection('papers')
      .where('projectId', '==', projectId)
      .get();

    // Count presentations
    const presentationsSnapshot = await ctx.firestore
      .collection('presentations')
      .where('projectId', '==', projectId)
      .get();

    // Get last activity
    const activitySnapshot = await ctx.firestore
      .collection('project_activities')
      .where('projectId', '==', projectId)
      .orderBy('createdAt', 'desc')
      .limit(1)
      .get();

    const lastActivity = activitySnapshot.empty 
      ? new Date() 
      : (() => {
          const doc = activitySnapshot.docs[0];
          if (!doc) return new Date();
          const data = doc.data();
          return data.createdAt.toDate();
        })();

    return {
      paperCount: papersSnapshot.size,
      presentationCount: presentationsSnapshot.size,
      totalFiles: papersSnapshot.size + presentationsSnapshot.size,
      lastActivity,
    };
  } catch {
    return {
      paperCount: 0,
      presentationCount: 0,
      totalFiles: 0,
      lastActivity: new Date(),
    };
  }
}

async function getProjectMembers(ctx: { firestore: admin.firestore.Firestore }, members: Array<{ uid: string; role: string; joinedAt: Date }>) {
  const memberDetails = await Promise.all(
    members.map(async (member) => {
      try {
        const userDoc = await ctx.firestore
          .collection('users')
          .doc(member.uid)
          .get();
        
        const userData = userDoc.data();
        return {
          uid: member.uid,
          email: userData?.email || '',
          name: userData?.name || 'Unknown User',
          role: member.role as 'viewer' | 'editor' | 'admin',
          joinedAt: member.joinedAt instanceof Date ? member.joinedAt : new Date(member.joinedAt),
        };
      } catch {
        return {
          uid: member.uid,
          email: '',
          name: 'Unknown User',
          role: member.role as 'viewer' | 'editor' | 'admin',
          joinedAt: new Date(),
        };
      }
    })
  );

  return memberDetails;
}

async function logActivity(ctx: { firestore: admin.firestore.Firestore; user: { uid: string; email?: string; displayName?: string } }, projectId: string, type: string, action: string, description: string, metadata?: Record<string, unknown>) {
  try {
    const userDoc = await ctx.firestore.collection('users').doc(ctx.user.uid).get();
    const userName = userDoc.data()?.name || 'Unknown User';

    await ctx.firestore
      .collection('project_activities')
      .add({
        projectId,
        type,
        action,
        description,
        createdAt: new Date(),
        createdBy: ctx.user.uid,
        createdByName: userName,
        metadata,
      });
  } catch {
    // Ignore activity tracking errors
  }
}