import { z } from 'zod';
import { router, protectedProcedure, createErrorHandler } from '../trpc';
import { TRPCError } from '@trpc/server';
import { cloudStorage, BUCKETS } from '../lib/storage';

// Input schemas
const uploadFileSchema = z.object({
  resourceId: z.string(), // paperId or presentationId
  resourceType: z.enum(['paper', 'slide']),
  fileName: z.string().min(1),
  fileData: z.string(), // base64 encoded file
  description: z.string().optional(),
});

const getFileSchema = z.object({
  fileId: z.string(),
  resourceId: z.string(),
  resourceType: z.enum(['paper', 'slide']),
});

const deleteFileSchema = z.object({
  fileId: z.string(),
  resourceId: z.string(),
  resourceType: z.enum(['paper', 'slide']),
});

const listFilesSchema = z.object({
  resourceId: z.string(),
  resourceType: z.enum(['paper', 'slide']),
});

// Output schemas
const fileSchema = z.object({
  id: z.string(),
  fileName: z.string(),
  fileType: z.string(),
  fileSize: z.number(),
  description: z.string().optional(),
  downloadUrl: z.string().optional(),
  uploadedBy: z.string(),
  uploadedAt: z.date(),
  version: z.number(),
});

export const storageRouter = router({
  // Upload a file
  uploadFile: protectedProcedure
    .input(uploadFileSchema)
    .output(fileSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        // Decode base64 file data
        const buffer = Buffer.from(input.fileData, 'base64');
        
        // Validate file size
        if (!cloudStorage.validateFileSize(buffer.length)) {
          throw new TRPCError({
            code: 'BAD_REQUEST',
            message: 'File size exceeds maximum limit (50MB)',
          });
        }

        // Detect file type
        const { fileTypeFromBuffer } = await import('file-type');
        const fileTypeResult = await fileTypeFromBuffer(buffer);
        const fileType = fileTypeResult?.mime || 'application/octet-stream';

        // Validate file type based on resource type
        const isValidType = input.resourceType === 'paper' 
          ? cloudStorage.validatePaperFile(fileType, input.fileName)
          : cloudStorage.validateSlideFile(fileType);

        if (!isValidType) {
          throw new TRPCError({
            code: 'BAD_REQUEST',
            message: `File type ${fileType} is not allowed for ${input.resourceType}s`,
          });
        }

        // Check resource access
        const collection = input.resourceType === 'paper' ? 'papers' : 'presentations';
        const resourceDoc = await ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .get();

        if (!resourceDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: `${input.resourceType} not found`,
          });
        }

        const resourceData = resourceDoc.data()!;
        if (resourceData.createdBy !== ctx.user.uid && 
            !resourceData.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Upload to Cloud Storage
        const bucketName = input.resourceType === 'paper' ? BUCKETS.PAPERS : BUCKETS.SLIDES;
        const storageFile = await cloudStorage.uploadFile({
          bucketName,
          userId: ctx.user.uid,
          resourceId: input.resourceId,
          fileName: input.fileName,
          fileType,
          fileSize: buffer.length,
          buffer,
        });

        // Save file metadata to Firestore
        const fileData = {
          fileName: storageFile.fileName,
          fileType: storageFile.fileType,
          fileSize: storageFile.fileSize,
          gcsPath: storageFile.gcsPath,
          description: input.description,
          uploadedBy: ctx.user.uid,
          uploadedAt: storageFile.uploadedAt,
          version: storageFile.version,
        };

        const fileRef = await ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .collection('files')
          .add(fileData);

        return {
          id: fileRef.id,
          ...fileData,
        };
      } catch (error) {
        throw createErrorHandler('Upload file')(error);
      }
    }),

  // Get a file with download URL
  getFile: protectedProcedure
    .input(getFileSchema)
    .output(fileSchema)
    .query(async ({ ctx, input }) => {
      try {
        // Check resource access
        const collection = input.resourceType === 'paper' ? 'papers' : 'presentations';
        const resourceDoc = await ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .get();

        if (!resourceDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: `${input.resourceType} not found`,
          });
        }

        const resourceData = resourceDoc.data()!;
        if (resourceData.createdBy !== ctx.user.uid && 
            !resourceData.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Get file metadata
        const fileDoc = await ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .collection('files')
          .doc(input.fileId)
          .get();

        if (!fileDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'File not found',
          });
        }

        const fileData = fileDoc.data()!;

        // Generate signed download URL
        const bucketName = input.resourceType === 'paper' ? BUCKETS.PAPERS : BUCKETS.SLIDES;
        const downloadUrl = await cloudStorage.getSignedUrl(bucketName, fileData.gcsPath);

        return {
          id: fileDoc.id,
          fileName: fileData.fileName,
          fileType: fileData.fileType,
          fileSize: fileData.fileSize,
          description: fileData.description,
          downloadUrl,
          uploadedBy: fileData.uploadedBy,
          uploadedAt: fileData.uploadedAt.toDate(),
          version: fileData.version,
        };
      } catch (error) {
        throw createErrorHandler('Get file')(error);
      }
    }),

  // List all files for a resource
  listFiles: protectedProcedure
    .input(listFilesSchema)
    .output(z.array(fileSchema))
    .query(async ({ ctx, input }) => {
      try {
        // Check resource access
        const collection = input.resourceType === 'paper' ? 'papers' : 'presentations';
        const resourceDoc = await ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .get();

        if (!resourceDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: `${input.resourceType} not found`,
          });
        }

        const resourceData = resourceDoc.data()!;
        if (resourceData.createdBy !== ctx.user.uid && 
            !resourceData.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Get all files
        const filesSnapshot = await ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .collection('files')
          .orderBy('uploadedAt', 'desc')
          .get();

        return filesSnapshot.docs.map(doc => {
          const data = doc.data();
          return {
            id: doc.id,
            fileName: data.fileName,
            fileType: data.fileType,
            fileSize: data.fileSize,
            description: data.description,
            uploadedBy: data.uploadedBy,
            uploadedAt: data.uploadedAt.toDate(),
            version: data.version,
          };
        });
      } catch (error) {
        throw createErrorHandler('List files')(error);
      }
    }),

  // Delete a file
  deleteFile: protectedProcedure
    .input(deleteFileSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        // Check resource access
        const collection = input.resourceType === 'paper' ? 'papers' : 'presentations';
        const resourceDoc = await ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .get();

        if (!resourceDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: `${input.resourceType} not found`,
          });
        }

        const resourceData = resourceDoc.data()!;
        if (resourceData.createdBy !== ctx.user.uid && 
            !resourceData.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Get file metadata
        const fileRef = ctx.firestore
          .collection(collection)
          .doc(input.resourceId)
          .collection('files')
          .doc(input.fileId);

        const fileDoc = await fileRef.get();
        if (!fileDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'File not found',
          });
        }

        const fileData = fileDoc.data()!;

        // Only creator or file uploader can delete
        if (resourceData.createdBy !== ctx.user.uid && fileData.uploadedBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Only the creator or file uploader can delete this file',
          });
        }

        // Delete from Cloud Storage
        const bucketName = input.resourceType === 'paper' ? BUCKETS.PAPERS : BUCKETS.SLIDES;
        await cloudStorage.deleteFile(bucketName, fileData.gcsPath);

        // Delete from Firestore
        await fileRef.delete();

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Delete file')(error);
      }
    }),
});