import { WebSocketServer, WebSocket } from 'ws';
import { Server as HTTPServer } from 'http';
import { verifyIdToken } from './lib/auth';
import { Logging } from '@google-cloud/logging';
// Type definitions matching the frontend
interface ChatMessage {
  id: string;
  content: string;
  role: 'user' | 'assistant' | 'system';
  timestamp: Date;
  attachments?: Attachment[];
  metadata?: Record<string, any>;
}

interface Attachment {
  id: string;
  name: string;
  type: string;
  size: number;
  url: string;
}

interface RTFAnalysisResult {
  id: string;
  fileName: string;
  status: 'processing' | 'completed' | 'failed';
  extractedContent?: {
    title?: string;
    sections: Array<{
      heading: string;
      content: string;
    }>;
    metadata?: Record<string, any>;
  };
  error?: string;
}

interface SOWGenerationResult {
  id: string;
  status: 'generating' | 'review_required' | 'approved' | 'rejected';
  content?: string;
  sections?: Array<{
    title: string;
    content: string;
    status: 'draft' | 'reviewed' | 'approved';
  }>;
  reviewComments?: string[];
  approvalInfo?: {
    approvedBy?: string;
    approvedAt?: Date;
    signature?: string;
  };
}

interface TaskProgress {
  taskId: string;
  taskName: string;
  status: 'pending' | 'in_progress' | 'completed' | 'failed';
  progress: number;
  message?: string;
  subtasks?: TaskProgress[];
  startTime?: Date;
  endTime?: Date;
}

const logging = new Logging();
const log = logging.log('maria-websocket');

interface AuthenticatedWebSocket extends WebSocket {
  userId?: string;
  sessionId?: string;
  isAlive?: boolean;
}

interface WebSocketMessage {
  type: 'chat_message' | 'progress_update' | 'task_status' | 'rtf_analysis' | 'sow_update' | 'error' | 'system';
  payload: unknown;
  sessionId?: string;
  timestamp: string;
}

export class WebSocketManager {
  private wss: WebSocketServer;
  private clients: Map<string, Set<AuthenticatedWebSocket>> = new Map();
  private sessionClients: Map<string, Set<AuthenticatedWebSocket>> = new Map();

  constructor(server: HTTPServer) {
    this.wss = new WebSocketServer({
      server,
      path: '/ws',
      verifyClient: async (info, cb) => {
        try {
          // Extract token from query parameters
          const url = new URL(info.req.url || '', `http://${info.req.headers.host}`);
          const token = url.searchParams.get('token');

          if (!token) {
            cb(false, 401, 'Unauthorized');
            return;
          }

          // Verify Firebase token
          const decodedToken = await verifyIdToken(token);
          if (!decodedToken) {
            cb(false, 401, 'Invalid token');
            return;
          }

          // Attach user info to request for later use
          (info.req as any).userId = decodedToken.uid;
          cb(true);
        } catch (error) {
          if (process.env.NODE_ENV === 'development') {
            console.error('WebSocket authentication error:', error);
          } else {
            log.error(log.entry({ severity: 'ERROR' }, { message: 'WebSocket authentication error', error }));
          }
          cb(false, 401, 'Authentication failed');
        }
      }
    });

    this.setupWebSocketServer();
    this.startHeartbeat();
  }

  private setupWebSocketServer() {
    this.wss.on('connection', (ws: AuthenticatedWebSocket, request) => {
      const userId = (request as any).userId;
      ws.userId = userId;
      ws.isAlive = true;

      // Add to user clients map
      if (!this.clients.has(userId)) {
        this.clients.set(userId, new Set());
      }
      this.clients.get(userId)!.add(ws);

      if (process.env.NODE_ENV === 'development') {
        console.log(`WebSocket client connected: ${userId}`);
      }

      ws.on('message', async (data) => {
        try {
          const message: WebSocketMessage = JSON.parse(data.toString());
          await this.handleMessage(ws, message);
        } catch {
          this.sendError(ws, 'Invalid message format');
        }
      });

      ws.on('pong', () => {
        ws.isAlive = true;
      });

      ws.on('close', () => {
        this.removeClient(ws);
      });

      ws.on('error', (error) => {
        if (process.env.NODE_ENV === 'development') {
          console.error('WebSocket error:', error);
        } else {
          log.error(log.entry({ severity: 'ERROR' }, { message: 'WebSocket error', error }));
        }
        this.removeClient(ws);
      });

      // Send connection confirmation
      this.sendMessage(ws, {
        type: 'system',
        payload: { message: 'Connected to MARIA WebSocket server' }
      });
    });
  }

  private async handleMessage(ws: AuthenticatedWebSocket, message: WebSocketMessage) {
    const { type, payload, sessionId } = message;

    // Associate session with WebSocket if provided
    if (sessionId && !ws.sessionId) {
      ws.sessionId = sessionId;
      if (!this.sessionClients.has(sessionId)) {
        this.sessionClients.set(sessionId, new Set());
      }
      this.sessionClients.get(sessionId)!.add(ws);
    }

    try {
      switch (type) {
        case 'chat_message':
          await this.handleChatMessage(ws, payload as ChatMessage);
          break;
        case 'rtf_analysis':
          await this.handleRTFAnalysis(ws, payload as { fileId: string; fileUrl: string });
          break;
        case 'sow_update':
          await this.handleSOWUpdate(ws, payload as any);
          break;
        case 'task_status':
          await this.handleTaskStatus(ws, payload as any);
          break;
        default:
          this.sendError(ws, `Unknown message type: ${type}`);
      }
    } catch (error) {
      if (process.env.NODE_ENV === 'development') {
        console.error('Message handling error:', error);
      } else {
        log.error(log.entry({ severity: 'ERROR' }, { message: 'Message handling error', error }));
      }
      this.sendError(ws, 'Failed to process message');
    }
  }

  private async handleChatMessage(ws: AuthenticatedWebSocket, chatMessage: ChatMessage) {
    // Import AI agents dynamically
    // const { createVertexAIClient } = await import('@maria/ai-agents');
    // const vertexAI = createVertexAIClient();
    // TODO: Implement conversation agent
    // const conversationAgent = new ConversationAgent(vertexAI);

    // TODO: Process message with AI using chatMessage
    const response = {
      content: `AI response placeholder for: ${chatMessage.content}`,
      rtfAnalysis: null,
      planId: null,
      sowDocument: null,
      executionPlan: null
    };

    // Send AI response
    const responseMessage: ChatMessage = {
      id: crypto.randomUUID(),
      content: response.content,
      role: 'assistant',
      timestamp: new Date(),
      metadata: {
        rtfConfidence: undefined,
        taskType: undefined,
        intent: undefined,
        complexity: undefined,
        planId: response.planId,
        hasSOW: !!response.sowDocument,
        executionSteps: undefined,
        responseLength: response.content.length,
      }
    };

    this.broadcastToSession(ws.sessionId!, {
      type: 'chat_message',
      payload: responseMessage
    });

    // Send RTF analysis if available
    if (response.rtfAnalysis) {
      this.sendMessage(ws, {
        type: 'rtf_analysis',
        payload: response.rtfAnalysis
      });
    }

    // Send SOW update if generated
    if (response.sowDocument) {
      this.sendMessage(ws, {
        type: 'sow_update',
        payload: response.sowDocument
      });
    }
  }

  private async handleRTFAnalysis(ws: AuthenticatedWebSocket, payload: { fileId: string; fileUrl: string }) {
    const { RTFParser, createVertexAIClient } = await import('@maria/ai-agents');
    
    const vertexAI = createVertexAIClient();
    const rtfParser = new RTFParser(vertexAI);

    // TODO: Download file from URL and extract text
    const fileContent = 'Sample RTF content'; // Placeholder

    const analysis = await rtfParser.parseRTF(fileContent);

    const result: RTFAnalysisResult = {
      id: payload.fileId,
      fileName: 'document.rtf', // Extract from URL
      status: 'completed',
      extractedContent: {
        title: analysis.task.intent,
        sections: [], // TODO: Extract sections
        metadata: analysis.metadata
      }
    };

    this.sendMessage(ws, {
      type: 'rtf_analysis',
      payload: result
    });
  }

  private async handleSOWUpdate(ws: AuthenticatedWebSocket, payload: any) {
    if (payload.action === 'generate') {
      const { SOWGenerator, createVertexAIClient } = await import('@maria/ai-agents');
      
      const vertexAI = createVertexAIClient();
      const sowGenerator = new SOWGenerator(vertexAI);

      const sowDocument = await sowGenerator.generateSOW(
        { requirements: payload.requirements },
        payload.metadata || {}
      );

      const result: SOWGenerationResult = {
        id: crypto.randomUUID(),
        status: 'review_required',
        content: JSON.stringify(sowDocument),
        sections: (sowDocument as any).sections?.map((section: any) => ({
          title: section.title,
          content: section.content,
          status: 'draft'
        }))
      };

      this.sendMessage(ws, {
        type: 'sow_update',
        payload: result
      });
    } else if (payload.action === 'approve' || payload.action === 'reject') {
      // Handle SOW approval/rejection
      this.broadcastToSession(ws.sessionId!, {
        type: 'sow_update',
        payload: {
          id: payload.sowId,
          status: payload.action === 'approve' ? 'approved' : 'rejected',
          comments: payload.comments
        }
      });
    }
  }

  private async handleTaskStatus(ws: AuthenticatedWebSocket, payload: any) {
    if (payload.action === 'pause' || payload.action === 'resume' || payload.action === 'restart') {
      // Broadcast task control actions to all session participants
      this.broadcastToSession(ws.sessionId!, {
        type: 'task_status',
        payload: {
          taskPlanId: payload.taskPlanId,
          action: payload.action,
          timestamp: new Date()
        }
      });
    } else if (payload.taskId) {
      // Request specific task progress
      const progress: TaskProgress = {
        taskId: payload.taskId,
        taskName: 'Sample Task', // TODO: Fetch from task plan
        status: 'in_progress',
        progress: 45,
        message: 'Processing data...'
      };

      this.sendMessage(ws, {
        type: 'progress_update',
        payload: progress
      });
    }
  }

  private sendMessage(ws: AuthenticatedWebSocket, message: Omit<WebSocketMessage, 'timestamp'>) {
    if (ws.readyState === WebSocket.OPEN) {
      ws.send(JSON.stringify({
        ...message,
        timestamp: new Date().toISOString()
      }));
    }
  }

  private sendError(ws: AuthenticatedWebSocket, error: string) {
    this.sendMessage(ws, {
      type: 'error',
      payload: { error }
    });
  }

  // Currently unused - for future implementation
  // private broadcastToUser(userId: string, message: Omit<WebSocketMessage, 'timestamp'>) {
  //   const userClients = this.clients.get(userId);
  //   if (userClients) {
  //     userClients.forEach(client => {
  //       this.sendMessage(client, message);
  //     });
  //   }
  // }

  private broadcastToSession(sessionId: string, message: Omit<WebSocketMessage, 'timestamp'>) {
    const sessionClients = this.sessionClients.get(sessionId);
    if (sessionClients) {
      sessionClients.forEach(client => {
        this.sendMessage(client, message);
      });
    }
  }

  private removeClient(ws: AuthenticatedWebSocket) {
    if (ws.userId) {
      const userClients = this.clients.get(ws.userId);
      if (userClients) {
        userClients.delete(ws);
        if (userClients.size === 0) {
          this.clients.delete(ws.userId);
        }
      }
    }

    if (ws.sessionId) {
      const sessionClients = this.sessionClients.get(ws.sessionId);
      if (sessionClients) {
        sessionClients.delete(ws);
        if (sessionClients.size === 0) {
          this.sessionClients.delete(ws.sessionId);
        }
      }
    }

    if (process.env.NODE_ENV === 'development') {
      console.log(`WebSocket client disconnected: ${ws.userId}`);
    }
  }

  private startHeartbeat() {
    setInterval(() => {
      this.wss.clients.forEach((ws: AuthenticatedWebSocket) => {
        if (ws.isAlive === false) {
          ws.terminate();
          return;
        }
        ws.isAlive = false;
        ws.ping();
      });
    }, 30000); // 30 seconds
  }

  // Public methods for external use
  public sendProgressUpdate(sessionId: string, progress: TaskProgress) {
    this.broadcastToSession(sessionId, {
      type: 'progress_update',
      payload: progress
    });
  }

  public sendTaskComplete(sessionId: string, taskId: string, results: any) {
    this.broadcastToSession(sessionId, {
      type: 'task_status',
      payload: {
        taskId,
        status: 'completed',
        results
      }
    });
  }
}