import { Storage } from '@google-cloud/storage';
import { v4 as uuidv4 } from 'uuid';
import path from 'path';

// Initialize Google Cloud Storage
const storage = new Storage({
  projectId: process.env.GOOGLE_CLOUD_PROJECT_ID || 'maria-code',
});

// Bucket configurations
export const BUCKETS = {
  PAPERS: process.env.GOOGLE_CLOUD_STORAGE_BUCKET_PAPERS || 'maria-papers-storage',
  SLIDES: process.env.GOOGLE_CLOUD_STORAGE_BUCKET_SLIDES || 'maria-slides-storage',
  SHARED: process.env.GOOGLE_CLOUD_STORAGE_BUCKET_SHARED || 'maria-shared-assets',
} as const;

// File type configurations
export const ALLOWED_PAPER_TYPES = [
  'application/pdf',
  'text/plain', // .tex, .bib
  'image/png',
  'image/jpeg',
  'image/svg+xml',
  'image/eps',
  'text/csv',
  'application/json',
  'application/xml',
  'application/zip',
] as const;

export const ALLOWED_SLIDE_TYPES = [
  'image/png',
  'image/jpeg',
  'image/gif',
  'image/svg+xml',
  'video/mp4',
  'video/webm',
  'video/quicktime',
  'application/pdf',
  'application/vnd.openxmlformats-officedocument.presentationml.presentation',
] as const;

export const MAX_FILE_SIZE = 50 * 1024 * 1024; // 50MB
export const SIGNED_URL_EXPIRY = parseInt(process.env.CLOUD_STORAGE_SIGNED_URL_EXPIRY || '3600'); // 1 hour

export interface FileUploadOptions {
  bucketName: string;
  userId: string;
  resourceId: string; // paperId or presentationId
  fileName: string;
  fileType: string;
  fileSize: number;
  buffer: Buffer;
}

export interface StorageFile {
  id: string;
  fileName: string;
  fileType: string;
  fileSize: number;
  gcsPath: string;
  downloadUrl?: string;
  uploadedBy: string;
  uploadedAt: Date;
  version: number;
}

export class CloudStorageService {
  /**
   * Upload a file to Google Cloud Storage
   */
  async uploadFile(options: FileUploadOptions): Promise<StorageFile> {
    const { bucketName, userId, resourceId, fileName, fileType, fileSize, buffer } = options;

    // Generate unique file path
    const fileId = uuidv4();
    const extension = path.extname(fileName);
    const sanitizedFileName = path.basename(fileName, extension).replace(/[^a-zA-Z0-9.-]/g, '_');
    const gcsPath = `${userId}/${resourceId}/${fileId}/${sanitizedFileName}${extension}`;

    // Get bucket reference
    const bucket = storage.bucket(bucketName);
    const file = bucket.file(gcsPath);

    // Upload file with metadata
    await file.save(buffer, {
      metadata: {
        contentType: fileType,
        cacheControl: 'public, max-age=3600',
        metadata: {
          uploadedBy: userId,
          resourceId,
          originalFileName: fileName,
          uploadedAt: new Date().toISOString(),
        },
      },
    });

    return {
      id: fileId,
      fileName,
      fileType,
      fileSize,
      gcsPath,
      uploadedBy: userId,
      uploadedAt: new Date(),
      version: 1,
    };
  }

  /**
   * Generate signed URL for file download
   */
  async getSignedUrl(bucketName: string, gcsPath: string): Promise<string> {
    const bucket = storage.bucket(bucketName);
    const file = bucket.file(gcsPath);

    const [signedUrl] = await file.getSignedUrl({
      action: 'read',
      expires: Date.now() + SIGNED_URL_EXPIRY * 1000,
      version: 'v4',
    });

    return signedUrl;
  }

  /**
   * Delete a file from storage
   */
  async deleteFile(bucketName: string, gcsPath: string): Promise<void> {
    const bucket = storage.bucket(bucketName);
    const file = bucket.file(gcsPath);

    await file.delete();
  }

  /**
   * Check if file exists
   */
  async fileExists(bucketName: string, gcsPath: string): Promise<boolean> {
    const bucket = storage.bucket(bucketName);
    const file = bucket.file(gcsPath);

    const [exists] = await file.exists();
    return exists;
  }

  /**
   * Get file metadata
   */
  async getFileMetadata(bucketName: string, gcsPath: string) {
    const bucket = storage.bucket(bucketName);
    const file = bucket.file(gcsPath);

    const [metadata] = await file.getMetadata();
    return metadata;
  }

  /**
   * List files in a directory
   */
  async listFiles(bucketName: string, prefix: string) {
    const bucket = storage.bucket(bucketName);

    const [files] = await bucket.getFiles({
      prefix,
    });

    return files.map(file => ({
      name: file.name,
      size: parseInt(file.metadata.size?.toString() || '0'),
      contentType: file.metadata.contentType,
      timeCreated: file.metadata.timeCreated,
      updated: file.metadata.updated,
    }));
  }

  /**
   * Validate file type for papers
   */
  validatePaperFile(fileType: string, fileName: string): boolean {
    // Check MIME type
    if (!ALLOWED_PAPER_TYPES.includes(fileType as any)) {
      return false;
    }

    // Additional validation for text files (LaTeX, BibTeX)
    if (fileType === 'text/plain') {
      const ext = path.extname(fileName).toLowerCase();
      return ['.tex', '.bib', '.txt'].includes(ext);
    }

    return true;
  }

  /**
   * Validate file type for slides
   */
  validateSlideFile(fileType: string): boolean {
    return ALLOWED_SLIDE_TYPES.includes(fileType as any);
  }

  /**
   * Validate file size
   */
  validateFileSize(fileSize: number): boolean {
    return fileSize > 0 && fileSize <= MAX_FILE_SIZE;
  }
}

// Export singleton instance
export const cloudStorage = new CloudStorageService();