import { z } from 'zod';
import { router, protectedProcedure, createErrorHandler, rateLimit } from '../trpc';
import { TRPCError } from '@trpc/server';

// Input schemas
const chatMessageSchema = z.object({
  content: z.string().min(1).max(4000),
  context: z.object({
    type: z.enum(['paper', 'presentation', 'project', 'general']).optional(),
    id: z.string().optional(), // paper/presentation/project ID
    metadata: z.record(z.any()).optional(),
  }).optional(),
});

const chatSessionSchema = z.object({
  title: z.string().min(1).max(200).optional(),
  context: z.object({
    type: z.enum(['paper', 'presentation', 'project', 'general']),
    id: z.string().optional(),
    metadata: z.record(z.any()).optional(),
  }).optional(),
});

const updateChatSessionSchema = z.object({
  sessionId: z.string(),
  title: z.string().min(1).max(200).optional(),
  archived: z.boolean().optional(),
});

// Output schemas
const messageSchema = z.object({
  id: z.string(),
  role: z.enum(['user', 'assistant', 'system']),
  content: z.string(),
  timestamp: z.date(),
  status: z.enum(['pending', 'completed', 'error']).optional(),
  metadata: z.record(z.any()).optional(),
});

const chatSessionOutputSchema = z.object({
  id: z.string(),
  title: z.string(),
  context: z.object({
    type: z.enum(['paper', 'presentation', 'project', 'general']),
    id: z.string().optional(),
    metadata: z.record(z.any()).optional(),
  }).optional(),
  createdAt: z.date(),
  updatedAt: z.date(),
  createdBy: z.string(),
  messageCount: z.number(),
  archived: z.boolean(),
  lastMessage: messageSchema.optional(),
});

export const chatRouter = router({
  // Create a new chat session
  createSession: protectedProcedure
    .use(rateLimit(20, 60 * 60 * 1000)) // 20 sessions per hour
    .input(chatSessionSchema)
    .output(chatSessionOutputSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const now = new Date();
        
        const sessionData = {
          title: input.title || 'New Chat',
          context: input.context,
          createdAt: now,
          updatedAt: now,
          createdBy: ctx.user.uid,
          messageCount: 0,
          archived: false,
        };

        const docRef = await ctx.firestore
          .collection('chat_sessions')
          .add(sessionData);

        return {
          id: docRef.id,
          ...sessionData,
          lastMessage: undefined,
        };
      } catch (error) {
        throw createErrorHandler('Create chat session')(error);
      }
    }),

  // Get all chat sessions for current user
  getSessions: protectedProcedure
    .input(z.object({
      archived: z.boolean().default(false),
      limit: z.number().min(1).max(100).default(20),
      cursor: z.string().optional(),
    }))
    .output(z.object({
      sessions: z.array(chatSessionOutputSchema),
      nextCursor: z.string().optional(),
    }))
    .query(async ({ ctx, input }) => {
      try {
        let query = ctx.firestore
          .collection('chat_sessions')
          .where('createdBy', '==', ctx.user.uid)
          .where('archived', '==', input.archived)
          .orderBy('updatedAt', 'desc');

        if (input.cursor) {
          const cursorDoc = await ctx.firestore
            .collection('chat_sessions')
            .doc(input.cursor)
            .get();
          if (cursorDoc.exists) {
            query = query.startAfter(cursorDoc);
          }
        }

        const snapshot = await query.limit(input.limit + 1).get();
        const sessions = [];
        let nextCursor: string | undefined;

        for (let i = 0; i < snapshot.docs.length; i++) {
          if (i === input.limit) {
            const cursorDoc = snapshot.docs[i];
            if (cursorDoc) {
              nextCursor = cursorDoc.id;
            }
            break;
          }

          const doc = snapshot.docs[i];
          if (!doc) continue;
          const data = doc.data();
          
          // Get last message
          const lastMessageSnapshot = await ctx.firestore
            .collection('chat_messages')
            .where('sessionId', '==', doc.id)
            .orderBy('timestamp', 'desc')
            .limit(1)
            .get();

          let lastMessage;
          if (!lastMessageSnapshot.empty) {
            const msgDoc = lastMessageSnapshot.docs[0];
            if (!msgDoc) continue;
            const msgData = msgDoc.data();
            lastMessage = {
              id: msgDoc.id,
              role: msgData.role,
              content: msgData.content,
              timestamp: msgData.timestamp.toDate(),
              status: msgData.status,
              metadata: msgData.metadata,
            };
          }

          sessions.push({
            id: doc.id,
            title: data.title,
            context: data.context,
            createdAt: data.createdAt.toDate(),
            updatedAt: data.updatedAt.toDate(),
            createdBy: data.createdBy,
            messageCount: data.messageCount || 0,
            archived: data.archived || false,
            lastMessage,
          });
        }

        return { sessions, nextCursor };
      } catch (error) {
        throw createErrorHandler('Get chat sessions')(error);
      }
    }),

  // Get a specific chat session with messages
  getSession: protectedProcedure
    .input(z.object({
      sessionId: z.string(),
      messageLimit: z.number().min(1).max(100).default(50),
      messageCursor: z.string().optional(),
    }))
    .output(z.object({
      session: chatSessionOutputSchema,
      messages: z.array(messageSchema),
      nextMessageCursor: z.string().optional(),
    }))
    .query(async ({ ctx, input }) => {
      try {
        // Get session
        const sessionDoc = await ctx.firestore
          .collection('chat_sessions')
          .doc(input.sessionId)
          .get();

        if (!sessionDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Chat session not found',
          });
        }

        const sessionData = sessionDoc.data()!;

        // Check access
        if (sessionData.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Get messages
        let messageQuery = ctx.firestore
          .collection('chat_messages')
          .where('sessionId', '==', input.sessionId)
          .orderBy('timestamp', 'asc');

        if (input.messageCursor) {
          const cursorDoc = await ctx.firestore
            .collection('chat_messages')
            .doc(input.messageCursor)
            .get();
          if (cursorDoc.exists) {
            messageQuery = messageQuery.startAfter(cursorDoc);
          }
        }

        const messageSnapshot = await messageQuery.limit(input.messageLimit + 1).get();
        const messages = [];
        let nextMessageCursor: string | undefined;

        for (let i = 0; i < messageSnapshot.docs.length; i++) {
          if (i === input.messageLimit) {
            const cursorDoc = messageSnapshot.docs[i];
            if (cursorDoc) {
              nextMessageCursor = cursorDoc.id;
            }
            break;
          }

          const doc = messageSnapshot.docs[i];
          if (!doc) continue;
          const data = doc.data();
          
          messages.push({
            id: doc.id,
            role: data.role,
            content: data.content,
            timestamp: data.timestamp.toDate(),
            status: data.status,
            metadata: data.metadata,
          });
        }

        const session = {
          id: input.sessionId,
          title: sessionData.title,
          context: sessionData.context,
          createdAt: sessionData.createdAt.toDate(),
          updatedAt: sessionData.updatedAt.toDate(),
          createdBy: sessionData.createdBy,
          messageCount: sessionData.messageCount || 0,
          archived: sessionData.archived || false,
          lastMessage: messages.length > 0 ? messages[messages.length - 1] : undefined,
        };

        return { session, messages, nextMessageCursor };
      } catch (error) {
        throw createErrorHandler('Get chat session')(error);
      }
    }),

  // Send a message in a chat session
  sendMessage: protectedProcedure
    .use(rateLimit(100, 60 * 60 * 1000)) // 100 messages per hour
    .input(z.object({
      sessionId: z.string(),
      message: chatMessageSchema,
    }))
    .output(z.object({
      userMessage: messageSchema,
      assistantMessage: messageSchema,
    }))
    .mutation(async ({ ctx, input }) => {
      try {
        // Verify session exists and user has access
        const sessionDoc = await ctx.firestore
          .collection('chat_sessions')
          .doc(input.sessionId)
          .get();

        if (!sessionDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Chat session not found',
          });
        }

        const sessionData = sessionDoc.data()!;
        if (sessionData.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        const now = new Date();

        // Create user message
        const userMessageData = {
          sessionId: input.sessionId,
          role: 'user' as const,
          content: input.message.content,
          timestamp: now,
          status: 'completed' as const,
          context: input.message.context,
        };

        const userMessageRef = await ctx.firestore
          .collection('chat_messages')
          .add(userMessageData);

        // Generate AI response
        const assistantResponse = await generateAIResponse(
          input.message.content,
          input.message.context
        );

        // Create assistant message
        const assistantMessageData = {
          sessionId: input.sessionId,
          role: 'assistant' as const,
          content: assistantResponse.content,
          timestamp: new Date(),
          status: 'completed' as const,
          metadata: assistantResponse.metadata,
        };

        const assistantMessageRef = await ctx.firestore
          .collection('chat_messages')
          .add(assistantMessageData);

        // Update session
        await ctx.firestore
          .collection('chat_sessions')
          .doc(input.sessionId)
          .update({
            updatedAt: new Date(),
            messageCount: (sessionData.messageCount || 0) + 2,
            title: sessionData.messageCount === 0 
              ? generateSessionTitle(input.message.content)
              : sessionData.title,
          });

        return {
          userMessage: {
            id: userMessageRef.id,
            ...userMessageData,
          },
          assistantMessage: {
            id: assistantMessageRef.id,
            ...assistantMessageData,
          },
        };
      } catch (error) {
        throw createErrorHandler('Send message')(error);
      }
    }),

  // Update chat session
  updateSession: protectedProcedure
    .input(updateChatSessionSchema)
    .output(chatSessionOutputSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const sessionRef = ctx.firestore
          .collection('chat_sessions')
          .doc(input.sessionId);
        
        const sessionDoc = await sessionRef.get();

        if (!sessionDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Chat session not found',
          });
        }

        const sessionData = sessionDoc.data()!;
        if (sessionData.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        const updateData: Record<string, unknown> = {
          updatedAt: new Date(),
        };

        if (input.title) updateData.title = input.title;
        if (input.archived !== undefined) updateData.archived = input.archived;

        await sessionRef.update(updateData);

        const updatedDoc = await sessionRef.get();
        const updatedData = updatedDoc.data()!;

        return {
          id: input.sessionId,
          title: updatedData.title,
          context: updatedData.context,
          createdAt: updatedData.createdAt.toDate(),
          updatedAt: updatedData.updatedAt.toDate(),
          createdBy: updatedData.createdBy,
          messageCount: updatedData.messageCount || 0,
          archived: updatedData.archived || false,
          lastMessage: undefined, // Could fetch if needed
        };
      } catch (error) {
        throw createErrorHandler('Update chat session')(error);
      }
    }),

  // Delete chat session
  deleteSession: protectedProcedure
    .input(z.object({ sessionId: z.string() }))
    .mutation(async ({ ctx, input }) => {
      try {
        const sessionRef = ctx.firestore
          .collection('chat_sessions')
          .doc(input.sessionId);
        
        const sessionDoc = await sessionRef.get();

        if (!sessionDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Chat session not found',
          });
        }

        const sessionData = sessionDoc.data()!;
        if (sessionData.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Delete all messages in the session
        const messagesSnapshot = await ctx.firestore
          .collection('chat_messages')
          .where('sessionId', '==', input.sessionId)
          .get();

        const batch = ctx.firestore.batch();
        messagesSnapshot.docs.forEach(doc => batch.delete(doc.ref));
        batch.delete(sessionRef);

        await batch.commit();

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Delete chat session')(error);
      }
    }),
});

// Helper functions
async function generateAIResponse(
  userMessage: string, 
  context: unknown
): Promise<{ content: string; metadata?: Record<string, unknown> }> {
  try {
    // Import the new AI conversation system
    const { PlannerAgent, createVertexAIClient, RTFParser } = await import('@maria/ai-agents');
    
    const vertexAI = createVertexAIClient();
    const planner = new PlannerAgent(vertexAI);
    const rtfParser = new RTFParser(vertexAI);

    // Type guard for context
    const typedContext = context as { type?: string; id?: string; [key: string]: unknown } | null;
    
    // Parse user message to understand intent
    const rtfStructure = await rtfParser.parseRTF(userMessage, {
      type: typedContext?.type,
      userProfile: {},
    });

    // Generate contextual response based on RTF analysis
    let response = '';
    const metadata: Record<string, unknown> = {
      rtfConfidence: rtfStructure.confidence,
      taskType: rtfStructure.task.type,
      intent: rtfStructure.task.intent,
      complexity: rtfStructure.metadata.complexity,
    };

    // Check if this is a planning request
    if (rtfStructure.task.scope !== 'single-action' || 
        rtfStructure.task.intent.includes('plan') ||
        rtfStructure.task.intent.includes('create') ||
        rtfStructure.task.intent.includes('develop')) {
      
      // Generate a task plan for complex requests
      const taskPlan = await planner.createTaskPlan(userMessage, {
        type: typedContext?.type as 'paper' | 'presentation' | 'project' | 'general',
      });

      response = generatePlanningResponse(taskPlan, rtfStructure);
      metadata.planId = taskPlan.id;
      metadata.hasSOW = !!(taskPlan as any).sowDocument;
      metadata.executionSteps = taskPlan.executionPlan.length;
    } else {
      // Generate conversational response for simple requests
      response = await generateConversationalResponse(userMessage, rtfStructure, typedContext);
    }

    metadata.responseLength = response.length;
    metadata.generatedAt = new Date().toISOString();
    metadata.contextType = typedContext?.type || 'general';

    return { content: response, metadata };
  } catch (error) {
    console.error('AI response generation failed, falling back to simple response:', error);
    
    // Fallback to original simple response system
    return generateFallbackResponse(userMessage, context);
  }
}

// Generate response for planning tasks
function generatePlanningResponse(taskPlan: any, rtfStructure: any): string {
  const steps = taskPlan.executionPlan.map((step: any, index: number) => 
    `${index + 1}. **${step.name}** (${step.estimatedTime}h)\n   ${step.description}`
  ).join('\n\n');

  return `I've analyzed your request and created a comprehensive plan for "${taskPlan.title}".

## Task Analysis
- **Type**: ${rtfStructure.task.type}
- **Complexity**: ${rtfStructure.metadata.complexity}
- **Scope**: ${rtfStructure.task.scope}

## Execution Plan
${steps}

## Next Steps
${taskPlan.sowDocument ? 
  'I\'ve also generated a detailed Statement of Work (SOW) with budget estimates, risk analysis, and timeline. Would you like me to share those details?' : 
  'Would you like me to start working on the first step, or would you prefer to refine the plan first?'
}

How would you like to proceed?`;
}

// Generate conversational response using AI
async function generateConversationalResponse(
  userMessage: string, 
  rtfStructure: any, 
  context: any
): Promise<string> {
  const { createVertexAIClient } = await import('@maria/ai-agents');
  const vertexAI = createVertexAIClient();
  const model = await vertexAI.getGeminiModel();

  const conversationPrompt = `
You are an AI assistant in the MARIA platform specialized in helping with ${rtfStructure.task.type} tasks.

User Message: "${userMessage}"
Intent: ${rtfStructure.task.intent}
Context Type: ${context?.type || 'general'}
Complexity: ${rtfStructure.metadata.complexity}

Generate a helpful, conversational response that:
1. Acknowledges the user's request
2. Provides specific, actionable guidance
3. Offers to help with next steps
4. Maintains a professional but friendly tone

Focus on being practical and helpful for ${rtfStructure.task.type} work.
`;

  try {
    const result = await model.generateContent(conversationPrompt);
    return result.response.candidates?.[0]?.content?.parts?.[0]?.text || 'I apologize, but I encountered an issue generating a response.';
  } catch (error) {
    console.error('Failed to generate conversational response:', error);
    return `I understand you're asking about "${userMessage}". I'm here to help with your ${rtfStructure.task.type} work. Could you provide more specific details about what you'd like assistance with?`;
  }
}

// Fallback response system (original implementation)
async function generateFallbackResponse(
  userMessage: string,
  context: unknown
): Promise<{ content: string; metadata?: Record<string, unknown> }> {
  let response = '';
  const metadata: Record<string, unknown> = {};

  // Type guard for context
  const typedContext = context as { type?: string; [key: string]: unknown } | null;

  // Context-aware responses
  if (typedContext?.type === 'paper') {
    response = generatePaperContextResponse(userMessage);
    metadata.contextType = 'paper';
  } else if (typedContext?.type === 'presentation') {
    response = generatePresentationContextResponse(userMessage);
    metadata.contextType = 'presentation';
  } else if (typedContext?.type === 'project') {
    response = generateProjectContextResponse(userMessage);
    metadata.contextType = 'project';
  } else {
    response = generateGeneralResponse(userMessage);
    metadata.contextType = 'general';
  }

  metadata.responseLength = response.length;
  metadata.generatedAt = new Date().toISOString();
  metadata.fallback = true;

  return { content: response, metadata };
}

