import express, { Express } from 'express';
import cors from 'cors';
import { createServer } from 'http';
import { Logging } from '@google-cloud/logging';
import { createExpressMiddleware } from '@trpc/server/adapters/express';
import { appRouter } from './routers';
import { createContext } from './trpc';
import { initializeFirebase } from './lib/firebase';
import { WebSocketManager } from './websocket';
import dotenv from 'dotenv';

// Export AppRouter type for client usage
export type { AppRouter } from './routers';

// Load environment variables
dotenv.config();

// Initialize Firebase Admin
initializeFirebase();

const app: Express = express();
const port = process.env.PORT || 3001;

// Initialize Google Cloud Logging
const logging = new Logging();
const log = logging.log('maria-core-api');

// Middleware
app.use(cors({
  origin: [
    'http://localhost:3000', // Next.js dev server
    'https://studio.maria-code.dev', // Production frontend
    'https://maria-studio-*.vercel.app', // Vercel preview deployments
  ],
  credentials: true,
}));

app.use(express.json({ limit: '10mb' }));
app.use(express.urlencoded({ extended: true, limit: '10mb' }));

// Health check endpoint
app.get('/health', (_req, res) => {
  res.json({ 
    status: 'ok', 
    timestamp: new Date().toISOString(),
    version: process.env.npm_package_version || '1.0.0',
    environment: process.env.NODE_ENV || 'development',
  });
});

// tRPC middleware
app.use(
  '/trpc',
  createExpressMiddleware({
    router: appRouter,
    createContext,
    onError: ({ error }) => {
      // Log additional context in development
      if (process.env.NODE_ENV === 'development') {
        console.error('tRPC Error:', error);
      } else {
        log.error(log.entry({ severity: 'ERROR' }, { message: 'tRPC Error', error: error.message }));
      }
    },
  })
);

// 404 handler
app.use('*', (req, res) => {
  res.status(404).json({
    error: 'Not Found',
    message: `Route ${req.originalUrl} not found`,
    availableRoutes: [
      'GET /health',
      'POST /trpc/[procedure]',
      'WS /ws (WebSocket endpoint)',
    ],
  });
});

// Global error handler
app.use((err: unknown, req: express.Request, res: express.Response) => {
  const errorMessage = { message: 'Express Error', error: err, url: req.url };
  if (process.env.NODE_ENV === 'development') {
    console.error('Express Error:', err);
  } else {
    log.error(log.entry({ severity: 'ERROR' }, errorMessage));
  }
  const error = err as { message?: string; stack?: string };
  res.status(500).json({
    error: 'Internal Server Error',
    message: process.env.NODE_ENV === 'development' ? error.message : 'Something went wrong',
    ...(process.env.NODE_ENV === 'development' && { stack: error.stack }),
  });
});

// Create HTTP server
const httpServer = createServer(app);

// Initialize WebSocket server
const wsManager = new WebSocketManager(httpServer);

// Export for use in other modules
export { wsManager };

// Graceful shutdown
const server = httpServer.listen(port, () => {
  if (process.env.NODE_ENV === 'development') {
    console.log(`ðŸš€ tRPC server listening on port ${port}`);
    console.log(`ðŸ”Œ WebSocket server listening on ws://localhost:${port}/ws`);
  } else {
    log.info(log.entry({ severity: 'INFO' }, { message: `tRPC server listening on port ${port}` }));
  }
});

// Handle server startup errors
server.on('error', (err: unknown) => {
  const error = err as { code?: string };
  const errorMessage = { message: 'Server error', error: err };
  
  if (process.env.NODE_ENV === 'development') {
    console.error('Server error:', err);
  } else {
    log.error(log.entry({ severity: 'ERROR' }, errorMessage));
  }
  
  if (error.code === 'EADDRINUSE') {
    const portMessage = { message: `Port ${port} is already in use` };
    if (process.env.NODE_ENV === 'development') {
      console.error(`Port ${port} is already in use`);
    } else {
      log.error(log.entry({ severity: 'ERROR' }, portMessage));
    }
    process.exit(1);
  } else {
    const unexpectedMessage = { message: 'Unexpected server error' };
    if (process.env.NODE_ENV === 'development') {
      console.error('Unexpected server error');
    } else {
      log.error(log.entry({ severity: 'ERROR' }, unexpectedMessage));
    }
    process.exit(1);
  }
});

// Handle graceful shutdown
process.on('SIGTERM', () => {
  if (process.env.NODE_ENV === 'development') {
    console.log('Received SIGTERM, shutting down gracefully...');
  } else {
    log.info(log.entry({ severity: 'INFO' }, { message: 'Received SIGTERM, shutting down gracefully' }));
  }
  server.close(() => {
    if (process.env.NODE_ENV === 'development') {
      console.log('Server closed');
    } else {
      log.info(log.entry({ severity: 'INFO' }, { message: 'Server closed' }));
    }
    process.exit(0);
  });
});

process.on('SIGINT', () => {
  if (process.env.NODE_ENV === 'development') {
    console.log('Received SIGINT, shutting down gracefully...');
  } else {
    log.info(log.entry({ severity: 'INFO' }, { message: 'Received SIGINT, shutting down gracefully' }));
  }
  server.close(() => {
    if (process.env.NODE_ENV === 'development') {
      console.log('Server closed');
    } else {
      log.info(log.entry({ severity: 'INFO' }, { message: 'Server closed' }));
    }
    process.exit(0);
  });
});

// Handle uncaught exceptions
process.on('uncaughtException', (err) => {
  if (process.env.NODE_ENV === 'development') {
    console.error('Uncaught Exception:', err);
  } else {
    log.error(log.entry({ severity: 'CRITICAL' }, { message: 'Uncaught Exception', error: err.message, stack: err.stack }));
  }
  process.exit(1);
});

process.on('unhandledRejection', (reason) => {
  if (process.env.NODE_ENV === 'development') {
    console.error('Unhandled Rejection:', reason);
  } else {
    log.error(log.entry({ severity: 'CRITICAL' }, { message: 'Unhandled Rejection', reason }));
  }
  process.exit(1);
});

export default app;