import { z } from 'zod';
import { router, protectedProcedure, editorProcedure, createErrorHandler } from '../trpc';
import { TRPCError } from '@trpc/server';

// Input schemas
const slideContentSchema = z.object({
  title: z.string().optional(),
  subtitle: z.string().optional(),
  body: z.string().optional(),
  imageUrl: z.string().optional(),
  chartData: z.any().optional(),
});

const slideSchema = z.object({
  id: z.string(),
  type: z.enum(['title', 'content', 'image', 'chart']),
  content: slideContentSchema,
});

const createPresentationSchema = z.object({
  title: z.string().min(1),
  description: z.string().optional(),
  templateId: z.enum(['blank', 'business', 'academic']).default('blank'),
  slides: z.array(slideSchema).optional(),
});

const updatePresentationSchema = z.object({
  id: z.string(),
  title: z.string().min(1).optional(),
  description: z.string().optional(),
  status: z.enum(['draft', 'in_progress', 'completed', 'archived']).optional(),
  slides: z.array(slideSchema).optional(),
  theme: z.string().optional(),
});

const aiGenerateSchema = z.object({
  presentationId: z.string(),
  slideId: z.string(),
  prompt: z.string().min(1),
  type: z.enum(['content', 'outline', 'bullets', 'summary']).default('content'),
});

// Output schemas
const presentationSchema = z.object({
  id: z.string(),
  title: z.string(),
  description: z.string().optional(),
  templateId: z.string(),
  theme: z.string(),
  status: z.enum(['draft', 'in_progress', 'completed', 'archived']),
  slides: z.array(slideSchema),
  slideCount: z.number(),
  createdAt: z.date(),
  updatedAt: z.date(),
  createdBy: z.string(),
  collaborators: z.array(z.string()),
  thumbnail: z.string().optional(),
  googleSlidesId: z.string().optional(),
});

export const slidesRouter = router({
  // Get all presentations for current user
  list: protectedProcedure
    .input(z.object({
      status: z.enum(['draft', 'in_progress', 'completed', 'archived']).optional(),
      limit: z.number().min(1).max(100).default(20),
      cursor: z.string().optional(),
    }))
    .output(z.object({
      presentations: z.array(presentationSchema),
      nextCursor: z.string().optional(),
    }))
    .query(async ({ ctx, input }) => {
      try {
        let query = ctx.firestore
          .collection('presentations')
          .where('createdBy', '==', ctx.user.uid)
          .orderBy('updatedAt', 'desc');

        if (input.status) {
          query = query.where('status', '==', input.status);
        }

        if (input.cursor) {
          const cursorDoc = await ctx.firestore
            .collection('presentations')
            .doc(input.cursor)
            .get();
          if (cursorDoc.exists) {
            query = query.startAfter(cursorDoc);
          }
        }

        const snapshot = await query.limit(input.limit + 1).get();
        const presentations = [];
        let nextCursor: string | undefined;

        for (let i = 0; i < snapshot.docs.length; i++) {
          if (i === input.limit) {
            const cursorDoc = snapshot.docs[i];
            if (cursorDoc) {
              nextCursor = cursorDoc.id;
            }
            break;
          }

          const doc = snapshot.docs[i];
          if (!doc) continue;
          const data = doc.data();
          
          presentations.push({
            id: doc.id,
            title: data.title,
            description: data.description,
            templateId: data.templateId,
            theme: data.theme,
            status: data.status,
            slides: data.slides || [],
            slideCount: data.slides?.length || 0,
            createdAt: data.createdAt.toDate(),
            updatedAt: data.updatedAt.toDate(),
            createdBy: data.createdBy,
            collaborators: data.collaborators || [],
            thumbnail: data.thumbnail,
            googleSlidesId: data.googleSlidesId,
          });
        }

        return { presentations, nextCursor };
      } catch (error) {
        throw createErrorHandler('List presentations')(error);
      }
    }),

  // Get a specific presentation
  get: protectedProcedure
    .input(z.object({ id: z.string() }))
    .output(presentationSchema)
    .query(async ({ ctx, input }) => {
      try {
        const doc = await ctx.firestore
          .collection('presentations')
          .doc(input.id)
          .get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Presentation not found',
          });
        }

        const data = doc.data()!;

        // Check if user has access
        if (data.createdBy !== ctx.user.uid && 
            !data.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        return {
          id: doc.id,
          title: data.title,
          description: data.description,
          templateId: data.templateId,
          theme: data.theme,
          status: data.status,
          slides: data.slides || [],
          slideCount: data.slides?.length || 0,
          createdAt: data.createdAt.toDate(),
          updatedAt: data.updatedAt.toDate(),
          createdBy: data.createdBy,
          collaborators: data.collaborators || [],
          thumbnail: data.thumbnail,
          googleSlidesId: data.googleSlidesId,
        };
      } catch (error) {
        throw createErrorHandler('Get presentation')(error);
      }
    }),

  // Create a new presentation
  create: editorProcedure
    .input(createPresentationSchema)
    .output(presentationSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const now = new Date();
        
        // Generate initial slides based on template
        const initialSlides = input.slides || generateTemplateSlides(input.templateId, input.title, input.description);

        const presentationData = {
          title: input.title,
          description: input.description,
          templateId: input.templateId,
          theme: getTemplateTheme(input.templateId),
          status: 'draft' as const,
          slides: initialSlides,
          slideCount: initialSlides.length,
          createdAt: now,
          updatedAt: now,
          createdBy: ctx.user.uid,
          collaborators: [],
        };

        const docRef = await ctx.firestore
          .collection('presentations')
          .add(presentationData);

        return {
          id: docRef.id,
          ...presentationData,
        };
      } catch (error) {
        throw createErrorHandler('Create presentation')(error);
      }
    }),

  // Update a presentation
  update: protectedProcedure
    .input(updatePresentationSchema)
    .output(presentationSchema)
    .mutation(async ({ ctx, input }) => {
      try {
        const docRef = ctx.firestore.collection('presentations').doc(input.id);
        const doc = await docRef.get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Presentation not found',
          });
        }

        const currentData = doc.data()!;

        // Check permissions
        if (currentData.createdBy !== ctx.user.uid && 
            !currentData.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        const updateData: Record<string, unknown> = {
          updatedAt: new Date(),
        };

        if (input.title) updateData.title = input.title;
        if (input.description !== undefined) updateData.description = input.description;
        if (input.status) updateData.status = input.status;
        if (input.theme) updateData.theme = input.theme;
        
        if (input.slides) {
          updateData.slides = input.slides;
          updateData.slideCount = input.slides.length;
        }

        await docRef.update(updateData);

        const updatedDoc = await docRef.get();
        const updatedData = updatedDoc.data()!;

        return {
          id: input.id,
          title: updatedData.title,
          description: updatedData.description,
          templateId: updatedData.templateId,
          theme: updatedData.theme,
          status: updatedData.status,
          slides: updatedData.slides || [],
          slideCount: updatedData.slides?.length || 0,
          createdAt: updatedData.createdAt.toDate(),
          updatedAt: updatedData.updatedAt.toDate(),
          createdBy: updatedData.createdBy,
          collaborators: updatedData.collaborators || [],
          thumbnail: updatedData.thumbnail,
          googleSlidesId: updatedData.googleSlidesId,
        };
      } catch (error) {
        throw createErrorHandler('Update presentation')(error);
      }
    }),

  // Delete a presentation
  delete: protectedProcedure
    .input(z.object({ id: z.string() }))
    .mutation(async ({ ctx, input }) => {
      try {
        const docRef = ctx.firestore.collection('presentations').doc(input.id);
        const doc = await docRef.get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Presentation not found',
          });
        }

        const data = doc.data()!;

        // Only creator can delete
        if (data.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Only the creator can delete this presentation',
          });
        }

        await docRef.delete();

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Delete presentation')(error);
      }
    }),

  // AI content generation
  generateContent: protectedProcedure
    .input(aiGenerateSchema)
    .output(z.object({
      content: slideContentSchema,
      success: z.boolean(),
    }))
    .mutation(async ({ ctx, input }) => {
      try {
        // Check presentation access
        const doc = await ctx.firestore
          .collection('presentations')
          .doc(input.presentationId)
          .get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Presentation not found',
          });
        }

        const data = doc.data()!;
        if (data.createdBy !== ctx.user.uid && 
            !data.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Simulate AI generation (in production, integrate with Vertex AI)
        const generatedContent = await simulateAIGeneration(input.prompt, input.type);

        return {
          content: generatedContent,
          success: true,
        };
      } catch (error) {
        throw createErrorHandler('Generate AI content')(error);
      }
    }),

  // Export to Google Slides
  exportToGoogleSlides: protectedProcedure
    .input(z.object({ 
      presentationId: z.string(),
      title: z.string().optional(),
    }))
    .output(z.object({
      googleSlidesId: z.string(),
      url: z.string(),
      success: z.boolean(),
    }))
    .mutation(async ({ ctx, input }) => {
      try {
        // Check presentation access
        const doc = await ctx.firestore
          .collection('presentations')
          .doc(input.presentationId)
          .get();

        if (!doc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Presentation not found',
          });
        }

        const data = doc.data()!;
        if (data.createdBy !== ctx.user.uid && 
            !data.collaborators?.includes(ctx.user.uid)) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Access denied',
          });
        }

        // Simulate Google Slides export (in production, use Google Slides API)
        const googleSlidesId = `gs_${Date.now()}`;
        const url = `https://docs.google.com/presentation/d/${googleSlidesId}/edit`;

        // Update presentation with Google Slides ID
        await ctx.firestore
          .collection('presentations')
          .doc(input.presentationId)
          .update({
            googleSlidesId,
            updatedAt: new Date(),
          });

        return {
          googleSlidesId,
          url,
          success: true,
        };
      } catch (error) {
        throw createErrorHandler('Export to Google Slides')(error);
      }
    }),

  // Add collaborator
  addCollaborator: editorProcedure
    .input(z.object({
      presentationId: z.string(),
      email: z.string().email(),
    }))
    .mutation(async ({ ctx, input }) => {
      try {
        const presentationRef = ctx.firestore.collection('presentations').doc(input.presentationId);
        const presentationDoc = await presentationRef.get();

        if (!presentationDoc.exists) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'Presentation not found',
          });
        }

        const presentationData = presentationDoc.data()!;
        if (presentationData.createdBy !== ctx.user.uid) {
          throw new TRPCError({
            code: 'FORBIDDEN',
            message: 'Only the creator can add collaborators',
          });
        }

        // Find user by email
        const userSnapshot = await ctx.firestore
          .collection('users')
          .where('email', '==', input.email)
          .limit(1)
          .get();

        if (userSnapshot.empty) {
          throw new TRPCError({
            code: 'NOT_FOUND',
            message: 'User not found',
          });
        }

        const userId = userSnapshot.docs[0]?.id;
        if (!userId) {
          throw new TRPCError({
            code: 'INTERNAL_SERVER_ERROR',
            message: 'Failed to get user ID',
          });
        }
        const collaborators = presentationData.collaborators || [];

        if (!collaborators.includes(userId)) {
          collaborators.push(userId);
          await presentationRef.update({ collaborators });
        }

        return { success: true };
      } catch (error) {
        throw createErrorHandler('Add collaborator')(error);
      }
    }),
});

// Helper functions
function generateTemplateSlides(templateId: string, title: string, description?: string) {
  switch (templateId) {
    case 'business':
      return [
        { id: '1', type: 'title' as const, content: { title, subtitle: description || 'Business Proposal' } },
        { id: '2', type: 'content' as const, content: { title: 'Executive Summary', body: '' } },
        { id: '3', type: 'content' as const, content: { title: 'Problem Statement', body: '' } },
        { id: '4', type: 'content' as const, content: { title: 'Our Solution', body: '' } },
        { id: '5', type: 'content' as const, content: { title: 'Market Analysis', body: '' } },
        { id: '6', type: 'content' as const, content: { title: 'Business Model', body: '' } },
        { id: '7', type: 'content' as const, content: { title: 'Financial Projections', body: '' } },
        { id: '8', type: 'content' as const, content: { title: 'Team', body: '' } },
        { id: '9', type: 'content' as const, content: { title: 'Timeline', body: '' } },
        { id: '10', type: 'content' as const, content: { title: 'Thank You', body: 'Questions?' } }
      ];
    case 'academic':
      return [
        { id: '1', type: 'title' as const, content: { title, subtitle: description || 'Research Presentation' } },
        { id: '2', type: 'content' as const, content: { title: 'Introduction', body: '' } },
        { id: '3', type: 'content' as const, content: { title: 'Literature Review', body: '' } },
        { id: '4', type: 'content' as const, content: { title: 'Research Questions', body: '' } },
        { id: '5', type: 'content' as const, content: { title: 'Methodology', body: '' } },
        { id: '6', type: 'content' as const, content: { title: 'Data Collection', body: '' } },
        { id: '7', type: 'content' as const, content: { title: 'Results', body: '' } },
        { id: '8', type: 'content' as const, content: { title: 'Analysis', body: '' } },
        { id: '9', type: 'content' as const, content: { title: 'Discussion', body: '' } },
        { id: '10', type: 'content' as const, content: { title: 'Conclusions', body: '' } },
        { id: '11', type: 'content' as const, content: { title: 'Future Work', body: '' } },
        { id: '12', type: 'content' as const, content: { title: 'References', body: '' } }
      ];
    default:
      return [
        { id: '1', type: 'title' as const, content: { title, subtitle: description || 'Presentation' } }
      ];
  }
}

function getTemplateTheme(templateId: string): string {
  switch (templateId) {
    case 'business':
      return 'business';
    case 'academic':
      return 'academic';
    default:
      return 'default';
  }
}

async function simulateAIGeneration(prompt: string, type: string) {
  // Simulate API call delay
  await new Promise(resolve => setTimeout(resolve, 1000));

  switch (type) {
    case 'outline':
      return {
        title: 'AI Generated Outline',
        body: `1. Introduction
   â€¢ Overview of ${prompt}
   â€¢ Key objectives

2. Main Points
   â€¢ First key concept
   â€¢ Supporting evidence
   â€¢ Real-world applications

3. Analysis
   â€¢ Data insights
   â€¢ Comparative analysis
   â€¢ Future implications

4. Conclusion
   â€¢ Summary of findings
   â€¢ Recommendations
   â€¢ Next steps`
      };

    case 'bullets':
      return {
        title: 'Key Points',
        body: `â€¢ ${prompt} - Main concept explained
â€¢ Supporting data and evidence
â€¢ Practical applications and examples
â€¢ Impact on current practices
â€¢ Future considerations and opportunities`
      };

    case 'summary':
      return {
        title: 'Summary',
        body: `This slide summarizes the key aspects of ${prompt}. The main points include the fundamental concepts, their practical applications, and the potential impact on the field. Further research and implementation strategies are recommended for optimal results.`
      };

    default:
      return {
        title: prompt.split(' ').slice(0, 5).join(' '),
        body: `${prompt}

This content explores the fundamental aspects and implications of the topic. Key considerations include:

1. Background and Context
The historical development and current state of the subject matter provide essential context for understanding its significance.

2. Core Concepts
Understanding the fundamental principles is crucial for effective implementation and application.

3. Practical Applications
Real-world use cases demonstrate the value and versatility of these concepts across various domains.

4. Future Outlook
Emerging trends and potential developments suggest exciting possibilities for continued growth and innovation.`
      };
  }
}