import { z } from 'zod';
import { router, protectedProcedure } from '../trpc';
import { Neo4jBloomService } from '../lib/neo4j-bloom';
import { TRPCError } from '@trpc/server';

const bloomService = new Neo4jBloomService({
  projectId: process.env.GCP_PROJECT_ID || 'maria-code',
  neo4jHost: process.env.NEO4J_HOST || '',
  neo4jDbId: process.env.NEO4J_DB_ID || ''
});

export const graphRouter = router({
  // Generate JWT URL for Neo4j Bloom
  getBloomUrl: protectedProcedure
    .input(
      z.object({
        perspective: z.string().optional(),
        search: z.string().optional(),
        ttl: z.number().min(60).max(3600).optional() // 1 min to 1 hour
      })
    )
    .mutation(async ({ ctx, input }) => {
      if (!ctx.user.email) {
        throw new TRPCError({
          code: 'UNAUTHORIZED',
          message: 'User email is required'
        });
      }
      const userEmail = ctx.user.email;
      
      // Verify user has access to the requested project
      // TODO: Add projectId to input schema when project-based access is implemented
      const userRole = await bloomService.verifyUserAccess(userEmail);

      if (!userRole) {
        throw new TRPCError({
          code: 'FORBIDDEN',
          message: 'You do not have access to this project\'s graph'
        });
      }

      const { url, expiresAt } = await bloomService.generateBloomURL(
        userEmail,
        userRole,
        input
      );

      return {
        url,
        expiresAt,
        role: userRole
      };
    }),

  // Get graph statistics
  getStats: protectedProcedure
    .query(async () => {
      // TODO: Query Neo4j for graph statistics
      return {
        nodes: {
          total: 0,
          byType: {}
        },
        relationships: {
          total: 0,
          byType: {}
        }
      };
    }),

  // Execute Cypher query (limited to read-only)
  query: protectedProcedure
    .input(
      z.object({
        cypher: z.string(),
        params: z.record(z.any()).optional()
      })
    )
    .mutation(async ({ input }) => {
      // Validate query is read-only
      // const readOnlyKeywords = ['MATCH', 'RETURN', 'WITH', 'WHERE', 'ORDER', 'LIMIT'];
      const writeKeywords = ['CREATE', 'DELETE', 'SET', 'MERGE', 'REMOVE'];
      
      const upperQuery = input.cypher.toUpperCase();
      const hasWriteOperation = writeKeywords.some(keyword => 
        upperQuery.includes(keyword)
      );

      if (hasWriteOperation) {
        throw new TRPCError({
          code: 'BAD_REQUEST',
          message: 'Only read queries are allowed'
        });
      }

      // TODO: Execute query via Neo4j driver
      return {
        records: [],
        summary: {
          nodesCreated: 0,
          relationshipsCreated: 0,
          resultAvailableAfter: 0,
          resultConsumedAfter: 0
        }
      };
    })
});